<template>
  <v-container fluid grid-list-md style="width: 95%; min-width: 95%; overflow: auto" >
    <v-tabs
        center-active
        @change="tabChange"
    >
      <v-tab v-for="tab in tabs" :key="tab.value">
        {{ tab.title }}
      </v-tab>
    </v-tabs>
    <DataPage
        :name="`${name}_${selectedGroup}`"
        :headers-source="headersSource"
        :items-source="itemsSource"
        :mapper="mapper"
        :header-labels="headerLabels"
        :header-filter="headerFilter"
        :data-filter="dataFilter"
        :extra-filters="extraFilters"
    />
  </v-container>
</template>

<script>
import DataPage from "@/components/DataPage";

export default {
  name: "TabDataPage",
  data() {
    return {
      selectedGroup: this.tabs.length > 0 ? this.tabs[0].value : '',
      headerFilter: header => header.group == 'ALL' || header.group.split('|').includes(this.selectedGroup),
      dataFilter: item => {
        return item.tabGroup && item.tabGroup.split('|').includes(this.selectedGroup)
      },
    };
  },
  computed: {
    selectedTab() {
      return this.tabs.find(t => t.value == this.selectedGroup)
    },
    searchableCol() {
      return this.headers.filter(h => h.searchable).map(h => h.value)
    },
    extraFilters() {
      return this.selectedTab.dataFilters
    }
  },
  methods: {
    tabChange(tab) {
      const tabItem = this.tabs[tab]
      this.selectedGroup = tabItem.value
      this.$emit('tabChange', tabItem.value)
    },
  },
  props: {
    name: String,
    tabs: Array,
    headersSource: Array,
    itemsSource:  Array,
    headerLabels: Array,
    mapper: Function,
  },
  components: {
    DataPage,
  }
}
</script>

<style scoped>
.row {
  width: fit-content;
}
</style>