export function convertValue(item) {
    if (!item) return item
    const newItem = {}
    // 增加tabGroup字段
    newItem['tabGroup'] = itemTypeDef[item.type]

    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = itemFieldDef[key] ? itemFieldDef[key](value) : value
    })
    newItem['attr_all'] = calcItemAttr(item)
    const s = calcItemSeries(item)
    newItem['attr_total_series'] = s[0]
    newItem['attr_max_series'] = s[1]
    if (item['req_hero1'] >= 0) {
        const hero = searchHero(item['req_hero1'])
        if (hero && hero.ke) {
            newItem['ke'] = 1
        }
    }
    // 携带者
    newItem['owner_friend'] = formatMultiLine(item['owner_friend'] || [], 5)
    newItem['owner_friend_cnt'] = item['owner_friend']?.length || 0
    newItem['owner_npc'] = formatMultiLine(item['owner_npc'] || [], 5)
    newItem['owner_npc_cnt'] = item['owner_npc']?.length || 0
    return newItem
}

export function calcItemAttr(item) {
    if (!item) return 0
    const attrs = Number(item['attr_atk'])
        + Number(item['attr_def'])
        + Number(item['attr_agi'])
        + Number(item['attr_fs'])
        + Number(item['attr_ss'])
        + Number(item['attr_ws'])
        + Number(item['attr_rs'])
        + Number(item['attr_ts'])
    return attrs > 0 ? attrs : ''
}

function calcItemSeries(item) {
    if (!item) return [0, 0]
    const series = [item['attr_fs'], item['attr_ss'], item['attr_ws'], item['attr_rs'], item['attr_ts']]
    return [series.reduce((a, b) => Number(a) + Number(b), 0), Math.max(...series)]
}