<template>
  <div>
    <DataPage
        :name="'suit'"
        :headersSource="headers"
        :itemsSource="items"
        :mapper="convertValue"
        :header-labels="headerLabels"
        :extra-filters="dataFilters"
    />
  </div>
</template>

<script>
import {convertValue, headerLabels, dataFilters, headers} from "@/config/suit-config";
import DataPage from "@/components/DataPage";

export default {
  name: "SuitDataView",
  data() {
    return {
      headerLabels,
      dataFilters,
      convertValue,
    };
  },
  computed: {
    headers() {
      return headers
    },
    items() {
      return Object.values(this.$store.state.SuitSource)
    }
  },
  components: {
    DataPage,
  }
}
</script>

