import Vue from 'vue'
import store from './store/'
import vuetify from './plugins/vuetify'
import router from './router'
import App from "@/App";
Vue.config.productionTip = false


new Vue({
  vuetify,
  store,
  router,
  beforeCreate() {
    this.$store.dispatch('initStore')
  },
  render: h => h(App)
}).$mount('#app')
