                      :items="col.items"
                      :label="col.label"
                      v-on:change="elementChanged(col.label)"
                      dense
                      chips
                      multiple/>
                </v-col>
              </template>
            </v-row>
          </v-card-text>
        </v-card>
        <v-card flat v-if="skill.base.isAura">
          <v-card-title>光环词条</v-card-title>
          <v-card-text>
            <v-row v-for="row in rows" :key="row.title" dense>
              <template v-for="col in row.cols">
                <v-col
                    cols="12"
                    :sm="col.wide || 1"
                    :key="col.label"
                    align-self="end"
                >
                  <v-text-field
                      v-if="col.type != 'combobox'"
                      :label="col.label"
                      v-model="skill.auraAttr[col.label]"
                      :suffix="col.suffix"
                      :class="skill.auraAttr[col.label] != col.defValue ? 'modified-input' : ''"
                      :disabled="col.mainClass && skill.base.classType == '无'"
                      style="width: 80%"
                      v-on:change="elementChanged(col.label)"
                      dense/>
                  <v-select
                      v-if="col.type == 'combobox'"
                      v-model="skill.auraAttr[col.label]"
