                no-resize
            />
          </v-col>
          <v-col cols="12" md="1" />
          <v-col cols="12" md="1">
<!--            <v-btn v-on:click="save">-->
<!--              <span class="text-subtitle-2">保存</span>-->
<!--            </v-btn>-->
            <v-btn v-on:click="submit" :disabled="!modified">
              <span class="text-subtitle-2">{{isEdit ? '保存' : '创建'}}</span>
            </v-btn>
            <p></p>
            <v-btn v-on:click="reset">
              <span class="text-subtitle-2">重置</span>
            </v-btn>
          </v-col>

        </v-row>
        <p />
        <v-divider />
        <p />
