<script>
import {Skill, config, importSkill} from "@/model/skill-sim";
import {localSave} from "@/utils/StorageUtil";
import {exportCsv} from "@/utils/CsvUtil";

const attrLayout = [
  {
    title: '属性',
    cols: [ '自身攻击', '自身防御', '自身轻功', '战场攻击', '战场防御', '战场轻功', '行动时序', '敌方攻击', '敌方防御', '敌方轻功', '敌方时序' ]
  },
  {
    title: '特性',
    cols: [ '移动距离', '攻击距离', '杀伤范围', '暴击', '双击', '出招', '主系出招']
  },
  {
    title: '攻击效果',
    cols: [ '威力', '命中', '伤害', '加受伤度', '主系威力', '主系命中', '主系伤害', '吸血' ]
  },
  {
    title: '防御效果',
    cols: [ '减威', '闪避', '减伤', '减受伤度', '主系减威', '主系闪避', '主系减伤', '反震', '反弹', '挪移'  ]
  },
  {
    title: '异常',
    cols: [ '异常', '异常概率', '异常回合' ]
  },
  {
    title: '免疫',
    cols: [ '免疫']
  },
]

const headers = [
  { text: '名称', value: 'name' },
  { text: '序号', value: 'sid' },
  { text: '分数', value: 'grade' },
  { text: '描述', value: 'desc', width: '60%' },
  { text: '操作', value: 'actions', sortable: false },
]

export default {
  name: "SkillCustomView",
  data() {
    return {
      bottom: false,
      modified: false,
      dialogDelete: false,
      tableSelected: [],
      deletingItem: null,
      skill: new Skill(),
      grade: {
        desc: '',
        detail: () => '',
      },
      totalGrade: "0.00",
      gradeDetail: '',
    };
  },
  methods: {
    save() {
      if (this.modified) {
        localSave('curSkill', this.skill);
        this.modified = false
      }
    },
    submit() {
      this.$store.dispatch('customSkill/updateCustomSkill', {name: this.skill.base.name, skill: new Skill(this.skill)});
      this.modified = false
    },
    editItem(item) {
      const skill = this.$store.state['customSkill'].customSKills.find(skill => skill.name === item.name)
      this.skill = new Skill(skill.skill)
      this.elementChanged()
    },
    deleteItem(item) {
      this.dialogDelete = true
      this.deletingItem = item
      //this.$store.dispatch('customSkill/deleteCustomSkill', {name: item.name});
    },
    deleteItemConfirm() {
      this.$store.dispatch('customSkill/deleteCustomSkill', {name: this.deletingItem.name});
      this.dialogDelete = false
    },
    elementChanged() {
      this.modified = true
      this.skill.fixAttr()
      this.grade = this.skill.calcGrade()
      this.totalGrade = this.grade.totalGrade.toFixed(2)
    },
    clickGrade() {
      this.gradeDetail = this.grade.detail()
      this.bottom = true
    },
    reset() {
      this.skill = new Skill()
      this.elementChanged()
    },
    importSkill() {
      const skill = importSkill(this.skill.base.id)
      if (skill != null) {
        this.skill = skill
        this.elementChanged()
      }
    },
    exportSkill() {
      const exportData = []
      this.tableSelected.forEach(item => {
        const skill = this.$store.state['customSkill'].customSKills.find(skill => skill.name === item.name)
        exportData.push(...skill.skill.exportSkill())
        exportData.push({'1技能名': item.desc})
      })
      if (exportData.length > 0) {
        exportCsv(exportData, 'games.csv')
      }
    }
  },
  computed: {
    isEdit() {
      return this.$store.state['customSkill'].customSKills.find(skill => skill.name === this.skill.base.name) !== undefined
    },
    rows() {
      const rows = attrLayout.map(row => {
        return {
          title: row.title,
          cols: row.cols
              // .filter(col => {
              //   return this.skill.base.classType != '无' || !config[col].mainClass
              // })
              .map(col => {
                const colConfig = config[col]
                return {
                  ...colConfig,
                  label: col,
                  suffix: colConfig.pct ? '%' : '',
                  defValue: config.defValue(col)
                }
              })
        }
      })
      return rows
    },
    tableHeaders() {
      return headers
    },
    tableItems() {
      return this.$store.state['customSkill'].customSKills
          .map(({name, skill}) => {
            const grade = skill.calcGrade()
            return {
              id: name,
              name,
              sid: skill.base.id,
              grade: grade.totalGrade.toFixed(2),
              desc: grade.desc,
              action: '删除',
            }
          })
    }
  }
  // created() {
  //   const skill = localLoad('curSkill')
  //   if (skill) {
  //     this.skill = new Skill(skill)
  //     this.elementChanged()
  //   }
  // },
}
</script>

<style scoped>
