import axios from "axios";

export function requestData(fileName, version) {
    if (!version) {
        return axios.request({
            url: `/data/${fileName}.json`,
            method: 'get',
        }).then(res => res.data)
    }
    return axios.request({
        url: `/data/${version.path}/${fileName}.json`,
        method: 'get',
    }).then(res => res.data)
}