export function convertValue(item) {
    const newItem = {...item}
    newItem['formation'] = []
    newItem['count'] = item['heroes'].length
    newItem['ke'] = false
    for (let i = 1; i <= 7; i++) {
        if (item['heroes'].length < i) {
            break
        }
        const hero = searchHero(item['heroes'][i - 1])
        newItem['hero' + i] = `[${hero.id}]${hero.name}`
        if (hero['ke']) {
            newItem['ke'] = true
        }
    }
    newItem['totalAttr'] = newItem['atk'] + newItem['def'] + newItem['agi'] + newItem['fs'] + newItem['ss'] + newItem['ws'] + newItem['rs'] + newItem['ts']
    return newItem
}

export const dataFilters = [
    {
        type: 'select',
        label: '氪金',
        value: [ '只看氪金', '不看氪金', '全部' ],
        selected: '全部',
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            if (value == '只看氪金') {
                return item.ke
            } else {
                return !item.ke
            }
        }
    },
]