import {localLoad, localSave} from "@/utils/StorageUtil";
import {Role} from "@/model/role-sim";


const state = () => ({
    customRoles: [],
})

const getters = {
    num: state => state.customRoles.length,
}

const actions = {
    async updateCustomRole({ commit }, payload) {
        await commit('updateCustomRole', payload)
        await commit('saveCustomRoles')
    },
    async deleteCustomRole({ commit }, payload) {
        await commit('deleteCustomRole', payload)
        await commit('saveCustomRoles')
    },
    async loadCustomRoles({ commit }) {
        await commit('loadCustomRoles')
    }
}

const mutations = {
    updateCustomRole(state, { name, role }) {
        const index = state.customRoles.findIndex(item => item.name === name)
        if (index === -1) {
            state.customRoles.push({ name, role })
        } else {
            state.customRoles[index].role = role
        }
    },
    deleteCustomRole(state, { name }) {
        const index = state.customRoles.findIndex(item => item.name === name)
        state.customRoles.splice(index, 1)
    },
    saveCustomRoles(state) {
        localSave('customRoles', state.customRoles)
    },
    loadCustomRoles(state) {
        const customRoles = localLoad('customRoles')
        if (customRoles && customRoles.length > 0) {
            state.customRoles = customRoles.map(obj => {
                return {
                    name: obj.name,
                    role: new Role(obj.role)
                }
            })
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}