export function convertValue(item) {
    const newItem = {}
    Object.entries(item).forEach(([key, value]) => {
        newItem[key] = value != 0 ? value : ''
    })
    const skill = searchSkill(item['skillId'])
    if (!skill) {
        return null
    }
    newItem['tabGroup'] = skillTypeDef[skill.type] || 'OTHER'
    newItem['name'] = skill['name']
    newItem['levelType'] = cf.skillLevel(skill['levelType'])

    if (item['specHitIncType']) {
        newItem['specHitInc'] = `${item['specHitIncType']}${item['specHitInc']}`
    }
    if (item['specHitDecType']) {
        newItem['specHitDec'] = `${item['specHitDecType']}${item['specHitDec']}`
    }
    if (item['specPowerIncType']) {
        newItem['specPowerInc'] = `${item['specPowerIncType']}${item['specPowerInc']}`
    }
    if (item['specPowerDecType']) {
        newItem['specPowerDec'] = `${item['specPowerDecType']}${item['specPowerDec']}`
    }
    if (item['specDamageIncType']) {
        newItem['specDamageInc'] = `${item['specDamageIncType']}${item['specDamageInc']}`
    }
    if (item['specDamageDecType']) {
        newItem['specDamageDec'] = `${item['specDamageDecType']}${item['specDamageDec']}`
    }
    if (item['specCastIncType']) {
        newItem['specCastInc'] = `${item['specCastIncType']}${item['specCastInc']}`
    }
    if (item['deBuffImmune']) {
        newItem['deBuffImmune'] = formatDeBuff(item['deBuffImmune'])
    }
    if (item['deBuffMake']) {
        newItem['deBuffMake'] = `[${formatDeBuff(item['deBuffMake'])}]${item['deBuffRound']}回合+${item['deBuffRate']}%`
    }
    // 特殊属性
    if (item['ext']) {
        newItem['ext'] = fmtSkillExtAttr(item['ext'])
    }

    return newItem
}

function formatDeBuff(debuffList) {
    const list = debuffList.split(' ').map(s => s.trim())
    // 将list用逗号分隔
    return list.join('/')
}