<template>
  <v-container fluid grid-list-md style="width: 95%; min-width: 95%; overflow: auto" >
    <DataPage
        :name="'talk-ref'"
        :headers-source="headers"
        :items-source="items"
        :mapper="convertValue"
        :header-labels="[]"
        :extra-filters="dataFilters"
    />
  </v-container>
</template>

<script>
import DataPage from "@/components/DataPage";
import {convertValue, headers, dataFilters} from "@/config/talk-ref-config";
export default {
  name: "TalkRefView",
  data() {
    return {
      convertValue,
      dataFilters
    };
  },
  computed: {
    headers() {
      return headers;
    },
    items() {
      return (this.$store.state.TalkRef['无引用']?.map(talk => {
        return {
          ...talk,
          group: '无引用',
        }
      }) || []).concat(
          this.$store.state.TalkRef['多处引用']?.map(talk => {
            return {
              ...talk,
              group: '多处引用',
            }
          }) || []
      ).concat(
          this.$store.state.TalkRef['重复对话']?.map(talk => {
            return {
              ...talk,
              group: '重复对话',
            }
          }) || []
      )
    }
  },
  components: {
    DataPage,
  }
}
</script>

