<template>
  <v-container style="width: fit-content;min-width: 100%">
    <v-expansion-panels flat accordion v-model="option" v-if="extraFilters && extraFilters.length > 0 || headerLabels && headerLabels.length > 0">
      <v-expansion-panel>
        <v-expansion-panel-header>
          选项
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <div v-for="filter in extraFilters" :key="filter.label">
            <v-card flat>
              <v-card-title class="text-subtitle-2">
                {{ filter.label }}
              </v-card-title>
              <v-card-text>
                <v-chip-group
                    column
                    mandatory
                    active-class="primary--text text--accent-4"
                    v-model="filter.selected"
                    v-if="filter.type === 'select'"
                >
                  <v-chip outlined v-for="filterItem in filter.value" :key="filterItem" :value="filterItem" >
                    {{ filterItem }}
                  </v-chip>
                </v-chip-group>
                <v-chip-group
                    column
                    multiple
                    active-class="primary--text text--accent-4"
                    v-model="filter.selected"
                    v-if="filter.type === 'multiSelect'"
                >
                  <v-chip outlined v-for="filterItem in filter.value" :key="filterItem" :value="filterItem" >
                    {{ filterItem }}
                  </v-chip>
                </v-chip-group>
              </v-card-text>
            </v-card>
          </div>
          <v-card flat>
            <v-card-title class="text-subtitle-2" v-if="headerLabels.length > 0">
              表头筛选
            </v-card-title>
            <v-card-text>
              <v-chip-group
                  column
                  multiple
                  active-class="primary--text text--accent-4"
                  v-model="selectedLabels"
                  v-on:change="settingChanged()"
              >
                <v-chip outlined v-for="headerLabel in headerLabels" :key="headerLabel.value" >
                  {{ headerLabel.text }}
                </v-chip>
              </v-chip-group>
            </v-card-text>
          </v-card>
          <slot name="options"></slot>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-text-field
        class="mb-2"
        style="max-width: 100%"
        v-model="search"
        append-icon="mdi-magnify"
        label="搜索"
        single-line
        hide-details
        clearable
    ></v-text-field>
    <div style="max-width: 100%; overflow: scroll">
      <div style="width: max-content">
        <v-data-table
            dense
            multi-sort
            show-group-by
            :footer-props="{ 'items-per-page-options': [50, -1] }"
            :calculate-widths="true"
            :headers="headers"
            :items="items"
            :page.sync="page"
            :items-per-page.sync="itemPerPage"
            :search="search"
            class="elevation-1 table"
        >
          <template v-for="col in slotCols" v-slot:[`item.${col.name}`]="{ item }">
            <a :key="`${col.name}_${item['id']}`" v-if="col.searchable && item[`${col.name}`]" v-on:click="doSearch">{{ item[`${col.name}`] }}</a>
            <a :key="`${col.name}_${item['id']}`" v-if="col.ext && item[`${col.name}`]" v-on:click="extClick($event, item[`${col.name}_ext`])">{{ item[`${col.name}`] }}</a>
            <div :key="`${col.name}_${item['id']}`" v-if="col.multiline && item[`${col.name}`]" style="white-space: pre-wrap;">{{ item[`${col.name}`] }}</div>
          </template>
        </v-data-table>
        <v-col>
          <v-btn v-on:click="prevPage">上一页</v-btn>
          <v-btn v-on:click="nextPage">下一页</v-btn>
        </v-col>
      </div>
    </div>
    <v-bottom-sheet
        v-model="sheet"
        inset
    >
      <v-sheet
          class="text-center"
      >
        <div style="overflow: scroll;height: fit-content">
          <v-textarea
              v-model="sheetContext"
              rows="12"
              readonly
              filled
              outlined
              hide-details
          ></v-textarea>
        </div>
      </v-sheet>
    </v-bottom-sheet>

  </v-container>
</template>

<script>
const {localSave, localLoad} = require("@/utils/StorageUtil");

function dataFilterFunc(filters) {
  return (item) => {
    let result = true
    if (filters) {
      filters.forEach(filter => {
        if (filter.selected) {
          result = result && filter.filterFunc(filter.selected, item)
        }
      })
    }
    return result
  }
}

function selectLabels(headerLabels) {
  let ret = []
  headerLabels.forEach((headerLabel, index) => {
    if (headerLabel.selected) {
      ret.push(index)
    }
  })
  return ret
}

export default {
  name: "DataPage",
  data() {
    return {
      search: '',
      option: 0,
      page: 1,
      itemPerPage: 50,
      sheet: false,
      sheetContext: '',
      // 筛选headerLabels中select=true的元素
      selectedLabels: selectLabels(this.headerLabels),
    };
  },
  computed: {
    headers() {
      return this.headersSource
          .filter(this.headerFilter)
          .filter(h => {
            if (h.label) {
              return this.selectedLabels.includes(this.headerLabels.findIndex(l => l.value == h.label))
            } else {
              return true
            }
          })
    },
    items() {
      if (!this.$store.getters['isReady']) {
        return []
      }
      return this.itemsSource.map(this.mapper).filter(this.dataFilter)
          .filter(dataFilterFunc(this.extraFilters))
    },
    slotCols() {
      return this.headers.filter(h => h.searchable || h.ext || h.multiline).map(h => {
        return {name: h.value, searchable: h.searchable, ext: h.ext, multiline: h.multiline}
      })
    },
  },
  methods: {
    doSearch(event) {
      this.search = event.target.innerText
    },
    extClick(event, extFunc) {
      this.sheetContext = extFunc()
      this.sheet = true
    },
    settingChanged() {
      const settings = {}
      settings['selectedLabels'] = this.selectedLabels
      localSave(`setting::${this.name}`, settings)
    },
    nextPage() {
      if (this.page * this.itemPerPage >= this.items.length) {
        return
      }
      this.page = this.page + 1
    },
    prevPage() {
      if (this.page <= 1) {
        return
      }
      this.page = this.page - 1
    },
  },
  created() {
    if (this.name) {
      try {
        const settings = localLoad(`setting::${this.name}`)
        if (settings) {
          this.selectedLabels = settings['selectedLabels']
        }
      } catch (e) {
        console.log(e)
      }
    }
  },
  props: {
    name: String,
    headersSource: Array,
    itemsSource:  Array,
    headerLabels: Array,
    mapper: Function,
    headerFilter: {
      Function,
      default: () => () => true
    },
    dataFilter: {
      Function,
      default: () => () => true
    },
    extraFilters: Array,
  },
}
</script>

<style scoped>
.row {
  width: fit-content;
}
</style>