import {searchSkill, searchSkillConfigs} from "@/utils/SourceDataUtil";
import {
    calcEnergyCostGp,
    calcTeamGradeFactor,
    ConvertSkillToAttrGroup,
    SkillAndConfig
} from "@/model/grade/grade-skill";
import {fmtSkillExtAttr} from "@/model/grade/fmt";
import {attrGroupMsgs} from "@/model/grade/attr-msg";
import {skillType} from "@/utils/TypeValueConverter";

export default {

    calcSkillGrade(skillId, option = {}) {
        const skill = searchSkill(skillId)
        if (skill == null) {
            return {totalGrade: 0, desc: ''}
        }
        const st = skillType(skill['type'])
        const skillConfigs = searchSkillConfigs(skillId) || [];
        let rangeFactor = 1
        if (skill['ext_range']) {
            if (skill['ext_range'] === '双人') {
                rangeFactor = 0.5
            }
        } else {
            rangeFactor = calcTeamGradeFactor(skill['attr_selfRange'])
        }
        const energyGp = skill['type'] == 8 ? calcEnergyCostGp(skill['attr_costEng'], rangeFactor > 0) : 0
        const sc = new SkillAndConfig(skillConfigs, { rangeFactor, energyGp, skillId });
        const ag = ConvertSkillToAttrGroup(sc, {...option, 'skillType': st, skillId });
        if (skill['ext_grade']) {
            ag['extGrade'] = skill['ext_grade'];
        }
        const g = ag.calcGrade()
        g['desc'] = attrGroupMsgs(ag)
        return g
    },

    calcTitleGrade(title) {
        if (title == null) {
            return {totalGrade: 0, desc: ''}
        }
        let titleConfigs = []
        if (title['ext_attrs']) {
            titleConfigs = title['ext_attrs']
                .map((attr) => {
                    return {
                        name: title['name'],
                        skillId: title['skillId'],
                        skillType: 10,
                        ext: attr,
                    }
                })
        }
        const sc = new SkillAndConfig(titleConfigs, { rangeFactor: 1, energyGp: 0, skillId: title['skillId'] });
        const ag = ConvertSkillToAttrGroup(sc, { skillType: 10, skillId: title['skillId'] });
        if (title['ext_grade']) {
            ag['extGrade'] = title['ext_grade'];
        }
        const g = ag.calcGrade()
        g['desc'] = attrGroupMsgs(ag)
        return g
    },
    fmtSkillExtAttr,
}