export const dataFilters = [
    {
        type: 'select',
        label: '氪金',
        value: [ '只看氪金', '不看氪金', '全部' ],
        selected: '全部',
        filterFunc: (value, suit) => {
            if (value == '全部') {
                return true
            }
            const equips = [suit['weaponId'], suit['armorId'], suit['petId']]
            for (const equip of equips) {
                if (equip == null) {
                    continue
                }
                const item = searchItemText(equip)
                if (item['ke'] || item['req_hero1'] > 0 && searchHero(item['req_hero1']).ke) {
                    return value == '只看氪金'
                }
            }
            return value == '不看氪金'
        }
    },
]

