const friendFilter = (value, select) => {
    return {
        type: 'select',
        label: '路线',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            const friend = searchFriendConf(item.index);
            if (friend) {
                if (friend.group == value) {
                    return true
                }
            } else {
                const hero = searchHero(item.index);
                if (hero['ke']) {
                    return value == '氪卡';
                }
                else {
                    return value == 'NPC'
                }
            }
            return false
        }
    }
}
const typeFilter = (value, select) => {
    return {
        type: 'select',
        label: '类型',
        value: value,
        selected: select,
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            return item.op == value;
        }
    }
}

