import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ItemDataView from "@/views/data/ItemDataView";
import SkillDataView from "@/views/data/SkillDataView";
import SuitDataView from "@/views/data/SuitDataView";
import SkillConfigView from "@/views/data/SkillConfigView";
import HeroDataView from "@/views/data/HeroDataView";
import EventView from "@/views/event/EventView";
import EventGroupView from "@/views/event/EventGroupView";
import EventPointView from "@/views/event/EventPointView";
import BattleTabView from "@/views/battle/BattleTabView";
import TalkRefView from "@/views/event/TalkRefView";
import ListTabView from "@/views/list/ListTabView";
import CustomSimulatorView from "@/views/simulator/CustomSimulatorView";
import LearnSimulatorView from "@/views/simulator/LearnSimulatorView";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    },
    {
        path: '/hero',
        name: 'hero',
        component: HeroDataView
    },
    {
        path: '/item',
        name: 'item',
        component: ItemDataView
    },
    {
        path: '/skill',
        name: 'skill',
        component: SkillDataView
    },
    {
        path: '/suit',
        name: 'suit',
        component: SuitDataView
    },
    {
        path: '/list',
        name: 'list',
        component: ListTabView
    },
    {
        path: '/skill-config',
        name: 'skill-config',
        component: SkillConfigView
    },
    {
        path: '/event',
        name: 'event',
        component: EventView
    },
    {
        path: '/event-group',
        name: 'event-group',
        component: EventGroupView
    },
    {
        path: '/event-point',
        name: 'event-point',
        component: EventPointView
    },
    {
        path: '/battle',
        name: 'battle',
        component: BattleTabView
    },
    {
        path: '/talk-ref',
        name: 'talk-ref',
        component: TalkRefView
    },
    {
        path: '/custom-sim',
        name: 'custom',
        component: CustomSimulatorView
    },
    {
        path: '/learn-sim',
        name: 'learn',
        component: LearnSimulatorView
    },
    {
        path: '/coming',
        name: 'coming',
        component: () => import('../views/ComingSoon.vue')
    },
]

const router = new VueRouter({
    routes,
    mode: 'hash',
    base: '/'
})

export default router
