
export function header(group, label, text, value, ext) {
    return {
        group: group,
        label: label,
        text: text,
        value: value,
        groupable: false,
        ...ext
    }
}

export function formatMultiLine(list, perLine) {
    if (!list || list.length == 0) {
        return ''
    }
    return list.reduce((acc, cur, index) => {
        if (index % perLine === 0) {
            acc.push([])
        }
        acc[acc.length - 1].push(cur)
        return acc
    }, []).map(row => row.join('， ')).join('\n')
}