import store from "@/store";
import gradeModel from "@/model/grade";
import {
    searchHero,
    searchItem,
    searchSkill,
    searchSkillConfigs,
    searchSuit,
    searchTitle
} from "@/utils/SourceDataUtil";
import {getStandardIteReq} from "@/utils/TypeValueConverter";

export function bindSkillAndExt() {
    Object.entries(store.state.SkillExtConfigSource).forEach(([id, ext]) => {
        const skill = searchSkill(id);
        const configs = searchSkillConfigs(id) || [];
        if (ext['range']) {
            skill['ext_range'] = ext['range']
        }
        if (ext['grade']) {
            skill['ext_grade'] = ext['grade'];
        }
        if (ext['attrs']) {
            skill['ext_attr'] = ext['attrs'];
            ext['attrs'].forEach(attr => {
                const config = configs.find(config => config['skillType'] === attr['type'])
                if (config) {
                    config['ext'] = attr
                } else {
                    const skill = searchSkill(id)
                    const newConfig = {
                        name: skill['name'],
                        skillId: id,
                        skillType: attr['type'],
                        ext: attr
                    }
                    store.state.SkillConfigSource.push(newConfig)
                    if (store.state.Indexes.SkillConfigIndex[id]) {
                        store.state.Indexes.SkillConfigIndex[id].push(newConfig)
                    } else {
                        store.state.Indexes.SkillConfigIndex[id] = [newConfig]
                    }
                }
            })
        }
    });

    Object.entries(store.state.TitleExtConfigSource).forEach(([id, ext]) => {
        const title = searchTitle(id);
        if (ext['range']) {
            title['ext_range'] = ext['range']
        }
        if (ext['grade']) {
            title['ext_grade'] = ext['grade'];
        }
        if (ext['attrs']) {
            title['ext_attrs'] = ext['attrs'];
        }
        const grade = gradeModel.calcTitleGrade(title)
        title['grade'] = Number(grade.totalGrade.toFixed(2))
        title['grade_ext'] = () => `${title['name']}分值计算\n` + grade.detail()
        title['gen_desc'] = grade['desc']
    })
}

export function calcSkillLevel() {
    Object.entries(store.state.ItemSource).forEach(([, item]) => {
        if (item.targetSkill > 0) {
            const skill = searchSkill(item.targetSkill)
            if (skill) {
                switch (skill.type) {
                    case 0:
                        skill['level'] = item.attr_ts;
                        break;
                    case 1:
                        skill['level'] = item.attr_fs;
                        break;
                    case 2:
                        skill['level'] = item.attr_ss;
                        break;
                    case 3:
                        skill['level'] = item.attr_ws;
                        break;
                    case 4:
                        skill['level'] = item.attr_rs;
                        break;
                }
            }
        }
    });
}

export function initTitleSkill() {
    Object.values(store.state.TitleConfigSource).forEach((title) => {
        title['id'] = title['skillId']
        title['type'] = 10
    });
}

export function calcSkillGrade() {
    Object.values(store.state.SkillSource).forEach(skill => {
        // 防功标记
        const defFlag = skill.type == 5 && skill.levelType == 7
        const grade = gradeModel.calcSkillGrade(skill.id, {defFlag: defFlag})
        skill['grade'] = grade.totalGrade.toFixed(2)
        skill['grade_ext'] = () => `${skill['name']}分值计算\n` + grade.detail()
        skill['gen_desc'] = grade['desc']
        // 护体分值
        if (skill.type == 5 && (skill.levelType == 4 || skill.levelType == 5)) {
            const gradeUp = gradeModel.calcSkillGrade(skill.id, {upFlag: true})
            skill['grade_up'] = gradeUp.totalGrade.toFixed(2)
            skill['grade_up_ext'] = () => `${skill['name']}护体分值计算\n` + gradeUp.detail()
        }
        // 防功使用效果计算
        if (defFlag) {
            const gradeUse = gradeModel.calcSkillGrade(skill.id, {useFlag: true, defFlag: true})
            skill['grade_up'] = gradeUse.totalGrade.toFixed(2)
            skill['grade_up_ext'] = () => `${skill['name']}防功使用分值计算\n` + gradeUse.detail()
        }
    });
}

export function calcHeroGrade() {
    Object.values(store.state.HeroSource).forEach(hero => {
        // 首先设置套装字段
        const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
        if (suit) {
            hero['suit'] = suit.name
            hero['suit_bs'] = suit.bsId
            hero['suit_fs'] = suit.fsId
            hero['suit_talent'] = suit.talentId
        }

        hero['grade_total'] = 0
        const addGradeFactor = (propName, gradeOrSkill, factor, desc) => {
            hero[propName] = hero[propName] || 0
            hero[`${propName}_detail`] = hero[`${propName}_detail`] || []
            if (!gradeOrSkill) {
                return
            }
            let grade
            if (!isNaN(gradeOrSkill)) {
                grade = gradeOrSkill
            } else {
                grade = Number(gradeOrSkill.grade) || 0
                desc = `${gradeOrSkill.name}(${desc})`
            }

            hero[propName] += grade * factor
            hero[`${propName}_detail`].push({
                grade: grade,
                factor: factor,
                desc: desc
            })
        }

        // 天赋分值
        addGradeFactor('grade_talent', searchSkill(hero['talent']), 1, '天赋')
        addGradeFactor('grade_ke', searchSkill(hero['talent']), 1, '天赋')
        addGradeFactor('grade_ke_talent', searchSkill(hero['talent']), 1, '天赋')
        addGradeFactor('grade_talent', searchSkill(hero['suit_talent']), 1, '套装天赋')
        addGradeFactor('grade_total', hero['grade_talent'], 1, '天赋')
        // 内功分值
        addGradeFactor('grade_bs', searchSkill(hero['equip_bs']), 1, '内功')
        addGradeFactor('grade_bs', searchSkill(hero['suit_bs']), 1, '套装内功')
        addGradeFactor('grade_total', hero['grade_bs'], 1, '内功')
        // 轻功分值
        addGradeFactor('grade_fs', searchSkill(hero['equip_fs']), 1, '轻功')
        addGradeFactor('grade_fs', searchSkill(hero['suit_fs']), 1, '套装轻功')
        addGradeFactor('grade_total', hero['grade_fs'], 1, '轻功')
        // 特技分值（武功出招特技分值0.4，武功栏特技按分值顺序排列，系数分别为0.8、0.4、0.2）
        hero['grade_ss'] = 0
        // 统计技能
        const skillIds = []
        for (let i = 0; i < 10; i++) {
            const s = hero['skills'][i]
            if (s['skill']) {
                skillIds.push({
                    id: s['skill'],
                    desc: `第${i + 1}格`
                })
            }
        }
        // 添加武功技能
        if (hero['equip_wp'] > 0) {
            const item = searchItem(hero['equip_wp'])
            if (item['useSkill'] > 0) {
                if (!skillIds.find(s => s.id == item['useSkill'])) {
                    skillIds.push({
                        id: item['useSkill'],
                        desc: '武器'
                    })
                }
            }
        }
        const ss = []
        for (let skillId of skillIds) {
            const skill = searchSkill(skillId.id)
            if (skill && skill['type'] == 8) {
                ss.push({
                    grade: Number(skill.grade) || 0,
                    desc: `${skill['name']}(${skillId.desc})`
                })
            } else if (skill['type'] == 5 && skill['levelType'] == 7) {
                ss.push({
                    grade: Number(skill.grade_up) || 0,
                    desc: `${skill['name']}(${skillId.desc})`
                })
            }
        }
        // 专精武功出招特技(只统计前10个武功)
        const trigger = []
        for (let skillId of skillIds) {
            const skill = searchSkill(skillId.id)
            if (skill && skill['trigger']) {
                let g = 0
                let gs = []
                skill['trigger'].forEach(id => {
                    const triggerSkill = searchSkill(id)
                    // 特技或防功
                    if (triggerSkill['type'] == 8) {
                        g += Number(triggerSkill.grade) || 0
                        gs.push({
                            grade: Number(triggerSkill.grade) || 0,
                            desc: `${triggerSkill['name']}`
                        })
                    } else if (triggerSkill['type'] == 5 && triggerSkill['levelType'] == 7) {
                        g += Number(triggerSkill.grade_up) || 0
                        gs.push({
                            grade: Number(triggerSkill.grade_up) || 0,
                            desc: `${triggerSkill['name']}`
                        })
                    }
                })
                trigger.push({
                    grade: g,
                    desc: `${skill['name']}(${skillId.desc}) `,
                    details: gs
                })
            }
        }
        // 对特技分值排序
        ss.sort((a, b) => b.grade - a.grade)
        hero['grade_ss'] = 0
        for (let i = 0; i < ss.length; i++) {
            if (i == 0) {
                addGradeFactor('grade_ss', ss[i].grade, 0.8, ss[i].desc)
                if (ss[i].grade > 10 && ss[i].grade <= 15) {
                    addGradeFactor('grade_ke', 10, 1, ss[i].desc)
                    addGradeFactor('grade_ke_ss', 10, 1, ss[i].desc)
                } else {
                    addGradeFactor('grade_ke', ss[i].grade, 1, ss[i].desc)
                    addGradeFactor('grade_ke_ss', ss[i].grade, 1, ss[i].desc)
                }
            } else if (i == 1) {
                addGradeFactor('grade_ss', ss[i].grade, 0.4, ss[i].desc)
            } else if (i == 2) {
                addGradeFactor('grade_ss', ss[i].grade, 0.2, ss[i].desc)
            }
        }
        addGradeFactor('grade_total', hero['grade_ss'], 1, '特技')
        trigger.sort((a, b) => b.grade - a.grade)
        hero['grade_ts'] = 0
        const factors = {
            0: 0.4,
            1: 0.2,
            2: 0.1
        }
        let keTriggerDone = false
        for (let i = 0; i < trigger.length; i++) {
            const factor = factors[i] || 0
            if (factor > 0) {
                trigger[i].details.forEach(d => {
                    addGradeFactor('grade_ts', d.grade, factor, `${trigger[i].desc} 触发 ${d.desc}`)
                    if (!keTriggerDone && trigger[i].desc.indexOf('武器') < 0) {
                        keTriggerDone = true
                        addGradeFactor('grade_ke', d.grade, 0.5, `${trigger[i].desc} 触发 ${d.desc}`)
                        addGradeFactor('grade_ke_ts', d.grade, 0.5, `${trigger[i].desc} 触发 ${d.desc}`)
                    }
                })
            }
        }
        addGradeFactor('grade_total', hero['grade_ts'], 1, '触发特技')
        // 称号
        const title = searchTitle(hero['title'])
        if (title && title['grade'] > 0) {
            addGradeFactor('grade_title', Number(title['grade']), 1, title['name'])
            addGradeFactor('grade_ke', Number(title['grade']), 1, `${title['name']}(称号)`)
            addGradeFactor('grade_ke_title', Number(title['grade']), 1, title['name'])
        }
        // 双手
        if (hero['attr_atkCnt'] == 2) {
            addGradeFactor('grade_ke', 1, 1, '两次攻击')
        }
        // 调和
        if (hero['roleRound'] < 4 && hero['mpKind'] == 2) {
            addGradeFactor('grade_ke', 1, 1, '内力调和')
        }
        addGradeFactor('grade_total', hero['grade_title'], 1, '称号')
    });
}

export function createSkillIndex() {
    store.state.Indexes.SkillIndex = {};
    Object.values(store.state.SkillSource).forEach(skill => {
        store.state.Indexes.SkillIndex[skill.id] = {};
    });
    // 建立技能配置映射
    Object.values(store.state.SkillSource).forEach(skill => {
        resolveSkillConfig(skill)
    });
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        resolveSkillOwner(hero)
    });
    // 洗武
    store.state.EventSource['xw'].forEach(event => {
        resolveXwEvent(event)
    });

    // 建立威力加成映射
    store.state.ListSource['match1'].forEach(match => {
        const skillIndex = store.state.Indexes.SkillIndex[match['matchSkillId']];
        skillIndex['match'] = skillIndex['match'] || [];
        skillIndex['match'].push(`[${match['itemId']}]${searchItem(match['itemId']).name}+${match['power']}`);
        const matchIndex = store.state.Indexes.MatchIndex[match['matchSkillId']] || [];
        matchIndex.push({type: 'item', ...match});
        store.state.Indexes.MatchIndex[match['matchSkillId']] = matchIndex;
    });
    store.state.ListSource['match2'].forEach(match => {
        const skillIndex = store.state.Indexes.SkillIndex[match['matchSkillId']];
        skillIndex['match'] = skillIndex['match'] || [];
        skillIndex['match'].push(`[${match['skillId']}]${searchSkill(match['skillId']).name}+${match['power']}`);
        const matchIndex = store.state.Indexes.MatchIndex[match['matchSkillId']] || [];
        matchIndex.push({type: 'skill', ...match});
        store.state.Indexes.MatchIndex[match['matchSkillId']] = matchIndex;
    });

    // 建立外功出招映射
    Object.values(store.state.SkillSource).forEach(skill => {
        if (skill['trigBy'] && skill['trigBy'] > 0) {
            const triggedSkillIndex = store.state.Indexes.SkillIndex[skill['trigBy']]
            triggedSkillIndex['trigger'] = triggedSkillIndex['trigger'] || [];
            const rate = skill['attr_trig'] ? skill['attr_trig'] : 0;
            let text = `${rate}% [${skill.id}]${skill.name}`
            if (skill['attrl_power'] > 0) {
                text += ` (${skill['attrl_power']})`
            }
            if (skill['type'] == 8) {
                text += `(特技)`
            } else if (skill['type'] == 5) {
                text += `(防功)`
            }
            triggedSkillIndex['trigger'].push(text);
            // 填充原始数据
            const triggerSkill = searchSkill(skill['trigBy'])
            triggerSkill['trigger'] = triggerSkill['trigger'] || [];
            triggerSkill['trigger'].push(skill['id']);
            if (skill['type'] == 8) {
                triggerSkill['trigger_effect'] = triggerSkill['trigger_effect'] || [];
                triggerSkill['trigger_effect'].push(skill['id']);
            }
        }
    });
}

function addSkillIndex(hero, skillId, option) {
    option = option || {};
    if (!skillId || skillId <= 0) {
        return
    }
    const skillIndex = option.title ? store.state.Indexes.TitleIndex[skillId] : store.state.Indexes.SkillIndex[skillId];
    if (!skillIndex) {
        return
    }
    const text = option.suffix ? `[${hero.id}]${hero.name}(${option.suffix})` : `[${hero.id}]${hero.name}`
    if (hero['friend']) {
        skillIndex['owner_friend'] = skillIndex['owner_friend'] || [];
        if (!skillIndex['owner_friend'].includes(text)) {
            skillIndex['owner_friend'].push(text);
        }
    } else {
        skillIndex['owner_npc'] = skillIndex['owner_npc'] || [];
        if (!skillIndex['owner_npc'].includes(text)) {
            skillIndex['owner_npc'].push(text);
        }
    }
}

function resolveSkillConfig(skill) {
    // 建立技能配置映射
    const skillConfigIndex = store.state.Indexes.SkillConfigIndex[skill.id] || []
    // 解析回合数和光环两个属性
    for (const skillConfig of skillConfigIndex) {
        if (Number(skillConfig['round']) > 0) {
            skill['config_round'] = skillConfig['round']
        }
        if (skillConfig['skillType'].includes('团队')) {
            skill['config_aura'] = '是'
        }
    }
}

function resolveSkillOwner(hero) {
    const addFunc = (skillId, option) => {
        addSkillIndex(hero, skillId, option)
    }
    // 内轻天赋称号
    addFunc(hero['equip_bs'])
    addFunc(hero['equip_fs'])
    addFunc(hero['talent'])
    addFunc(hero['title'], {title: true})

    // 套装
    const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
    if (suit) {
        addFunc(suit.bsId, {suffix: '套装'})
        addFunc(suit.fsId, {suffix: '套装'})
        addFunc(suit.talentId, {suffix: '套装'})
    }

    // 武器武功
    if (hero['equip_wp'] > 0) {
        const item = searchItem(hero['equip_wp'])
        if (item['useSkill'] > 0) {
            addFunc(item['useSkill'], {suffix: '武器'})
        }
    }

    // 武功栏位
    hero['skills'].forEach((skill) => {
        const skillId = skill['skill']
        if (skillId <= 0) {
            return
        }
        // 内轻防功特殊处理
        const skillData = searchSkill(skillId)
        if (skillData.type == 5 || skillData.type == 6) {
            if (skillData.levelType == 7) {
                addFunc(skillId, {suffix: `防功`})
            }
        } else {
            // 专精后缀
            if (hero['mastery'] == skillId) {
                addFunc(skillId, {suffix: `专精`})
            } else {
                addFunc(skillId)
            }
        }
    })
}

function resolveXwEvent(event) {
    const heroId = event['index']
    const skillId = event['index2']
    const slot = event['value']
    const hero = searchHero(heroId)
    const op = event['op']
    if (op == '洗武功') {
        addSkillIndex(hero, skillId, {suffix: `洗${slot + 1}格`})
    } else if (op == '洗天赋') {
        addSkillIndex(hero, skillId, {suffix: `洗天赋`})
    } else if (op == '洗专精') {
        addSkillIndex(hero, skillId, {suffix: `洗专精`})
    }
}

export function initItemIndex() {
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        for (let i = 0; i < 4; i++) {
            const it = hero['items'][i]
            if (it['item'] && it['item'] > 0) {
                const item = searchItem(it['item'])
                if (hero['friend']) {
                    item['owner_friend'] = item['owner_friend'] || [];
                    item['owner_friend'].push(`[${hero.id}]${hero.name}`);
                } else {
                    item['owner_npc'] = item['owner_npc'] || [];
                    item['owner_npc'].push(`[${hero.id}]${hero.name}`);
                }
            }
        }
    });
    // 建立tags
    Object.values(store.state.ItemSource).forEach(item => {
        initItemTag(item)
    });
}

function initItemTag(item) {
    // 如果有限定门派、血族、人物
    // 如果外功秘籍要求用毒
    // 如果外功秘籍要求特殊资质
    // 如果外功秘籍增加双系或多系系数
    if (item['type'] != 2) {
        return
    }
    item['tags'] = item['tags'] || []
    const ne = v => {
        return v != -1
    }
    if (ne(item['req_hero1']) || ne(item['req_hero2']) || ne(item['req_gender']) || ne(item['req_party']) || ne(item['req_family'])) {
        item['tags'].push('限定')
    }
    if (item.targetSkill && item.targetSkill > 0) {
        // 双系
        const series = [item['attr_fs'], item['attr_ss'], item['attr_ws'], item['attr_rs'], item['attr_ts']]
        const [total, max] = series.reduce((acc, cur) => {
            acc[0] += cur
            if (cur > acc[1]) {
                acc[1] = cur
            }
            return acc
        }, [0, 0])

        const skill = searchSkill(item.targetSkill)
        // 设置级别
        switch (skill.type) {
            case 0:
                item['level'] = item['attr_ts'];
                item['bookType'] = '暗毒'
                break;
            case 1:
                item['level'] = item['attr_fs'];
                item['bookType'] = '拳掌'
                break;
            case 2:
                item['level'] = item['attr_ss'];
                item['bookType'] = '御剑'
                break;
            case 3:
                item['level'] = item['attr_ws'];
                item['bookType'] = '兵器'
                break;
            case 4:
                item['level'] = item['attr_rs'];
                item['bookType'] = '指腿'
                break;
            case 5:
                item['level'] = skill['levelType']
                item['bookType'] = '内功'
                break;
            case 6:
                item['level'] = skill['levelType']
                item['bookType'] = '轻功'
                break;
            default:
                item['bookType'] = '特殊'
                break;
        }

        if (skill.type >= 0 && skill.type <= 4) {
            if (item['req_poiMk'] > 0) {
                item['tags'].push('用毒')
            }
            if (total != max) {
                item['tags'].push('双系')
            }
            const iteReq = getStandardIteReq(item['level'])
            if (iteReq > 0 && iteReq != item['req_itl']) {
                if (item['req_itl'] > iteReq) {
                    item['tags'].push('高资')
                } else {
                    if (!item['tags'].includes('用毒') && !item['tags'].includes('限定')) {
                        item['tags'].push('低资')
                    }
                }
            }
        }
    } else {
        const series = [item['attr_fs'], item['attr_ss'], item['attr_ws'], item['attr_rs'], item['attr_ts']]
        if (series.reduce((acc, cur) => acc + cur, 0) > 0) {
            item['bookType'] = '特殊'
        }
    }
}

export function initSuitIndex() {
    // 建立拥有者映射
    Object.values(store.state.HeroSource).forEach(hero => {
        resolveSuitOwner(hero)
    });
}

function resolveSuitOwner(hero) {
    const suit = searchSuit(hero['equip_wp'], hero['equip_ar'], hero['equip_pt'])
    if (suit) {
        if (hero['friend']) {
            suit['owner_friend'] = suit['owner_friend'] || [];
            suit['owner_friend'].push(`[${hero.id}]${hero.name}`);
        } else {
            suit['owner_npc'] = suit['owner_npc'] || [];
            suit['owner_npc'].push(`[${hero.id}]${hero.name}`);
        }
    }
}