import {AttrPrefixMap} from "@/model/grade/attr";


export function fmtSkillExtAttr(attrs) {
    let ret = []
    // values
    if (attrs.values) {
        attrs.values.forEach(({attr, value}) => {
            const attrConfig = AttrPrefixMap[attr]
            if (attrConfig) {
                attr = attrConfig.name || attr
                ret.push(attrConfig.print(attr, value))
            } else {
                ret.push(`${attr}:${value}`)
                console.log(`未知属性:${attr}`)
            }
        })
    }
    // debuffs
    if (attrs.debuffs) {
        attrs.debuffs.forEach(({attr, value, rate}) => {
            const attrConfig = AttrPrefixMap[attr]
            if (attrConfig) {
                attr = attrConfig.name || attr
                ret.push(attrConfig.print(attr, value, rate))
            } else {
                ret.push(`${attr}:${value}, ${rate}`)
                console.log(`未知属性:${attr}`)
            }
        })
    }
    // immunes
    if (attrs.immunes) {
        ret.push(`免疫${attrs.immunes.join('/')}`)
    }
    // flags
    if (attrs.flags) {
        attrs.flags.forEach(value => {
            ret.push(value)
        })
    }
    return ret.join('，')
}