import gc from "@/model/grade/grade-config";
import {AttrPrefixMap, CONDITIONS} from "@/model/grade/attr";

function f0(num) {
    const ret = num.toFixed(0);
    return ret >= 0 ? ret : `(${ret})`;
}

function f2(num) {
    const ret = num.toFixed(2);
    return ret >= 0 ? ret : `(${ret})`;
}

export class GradeEntry {
    constructor(type) {
        this.type = type
        this.logs = []
        this.gradePoint = 0.0
    }

    log(msg) {
        this.logs.push(msg)
    }

    toString() {
        return `${this.type}: ${this.gradePoint.toFixed(2)}`
    }

    detail() {
        return this.logs.join("\n")
    }
}

export class GradeEntries {
    constructor(factor) {
        this.entries = []
        this.factor = factor || 1.0
        this.totalGrade = 0
    }

    addEntry(entry) {
        if (entry && entry.gradePoint != 0) {
            this.entries.push(entry)
        }
    }

    calcTotalGradePoint() {
        let total = 0.0
        this.entries.forEach(entry => {
            total += entry.gradePoint
        })
        this.totalGrade = total * this.factor
        return {base: total, final: this.totalGrade}
    }

    toString(name) {
        name = name || ''
        const res = this.calcTotalGradePoint()
        const str = this.entries.map(entry => entry.toString()).join('，')
        if (this.factor != 1.0) {
            return `${name}分值：${res.final.toFixed(2)}（${res.base.toFixed(2)} * ${this.factor})，[${str}]`
        } else {
            return `${name}分值：${res.final.toFixed(2)}，[${str}]`
        }
    }

    detail() {
        const str = this.entries.map(entry => `[${entry.toString()}]\n${entry.detail()}\n`).join('\n')
        return str
    }
}

export function convertAttrToGrade(ae, factor) {
    let ges = new GradeEntries(factor)
    ges.addEntry(speedGradeEntry(ae))
    ges.addEntry(atkGradeEntry(ae))
    ges.addEntry(defGradeEntry(ae))
    ges.addEntry(atkSoftGradeEntry(ae))
    ges.addEntry(defSoftGradeEntry(ae))
    ges.addEntry(critGradeEntry(ae))
    ges.addEntry(doubleHitGradeEntry(ae))
    ges.addEntry(castGradeEntry(ae))
    ges.addEntry(rangeGradeEntry(ae))
    ges.addEntry(suckGradeEntry(ae))
    ges.addEntry(injuryGradeEntry(ae))
    ges.addEntry(counterGradeEntry(ae))
    ges.addEntry(debuffGradeEntry(ae))
    ges.addEntry(immuneGradeEntry(ae))
    ges.addEntry(extGradeEntry(ae))
    return ges
}

function speedGradeEntry(ae) {
    const ge = new GradeEntry('时序分')

    // 计算自身属性增幅
    const atk = gc.getTotalAtk(ae)
    const def = gc.getTotalDef(ae)
    const agi = gc.getTotalAgi(ae)
    const ser = gc.getTotalSeriesSpeed(ae)
    if (atk != 0 || def != 0 || agi != 0 || ser != 0) {
        ge.log(`自身属性增幅：总攻击${f0(atk)}，总防御${f0(def)}，总轻功${f0(agi)}，平均系数${f0(ser)}`)
    }

    // 计算敌方属性降幅
    const enemyAtkDec = -gc.constants['标准敌方场攻'] * ae.getValueInc('敌攻')
    const enemyDefDec = -gc.constants['标准敌方场防'] * ae.getValueInc('敌防')
    const enemyAgiDec = -gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    if (enemyAtkDec != 0 || enemyDefDec != 0 || enemyAgiDec != 0) {
        ge.log(`敌方属性降幅：攻击${f0(enemyAtkDec)}，防御${f0(enemyDefDec)}，轻功${f0(enemyAgiDec)}`)
    }

    // 计算时序系数
    const asp = ae.getValueInc('时序')
    const enemyAsp = -ae.getValueInc('敌时')
    if (asp != 0 || enemyAsp != 0) {
        ge.log(`时序系数：自身时序增加${f2(asp)}，敌方时序降低${f2(enemyAsp)}`)
    }

    // 读取常数
    const baseSpeed = gc.constants['标准时序']
    const enemyBaseSpeed = gc.constants['标准敌方时序']
    const speedFactor = gc.constants['时序分值系数']
    ge.log(`常数：标准时序${baseSpeed}，标准敌方时序${enemyBaseSpeed}，时序分值系数${speedFactor}`)

    // 进行计算
    const sp = atk + def + agi * 2 + ser
    if (sp != 0) {
        ge.log(`自身属性时序增加值 = 总攻击+总防御+总轻功*2+平均系数 = ${f0(atk)}+${f0(def)}+${f0(agi)}*2+${f0(ser)} = ${f0(sp)}`)
    }
    const sr = (baseSpeed + sp) * (1 + asp)
    if (sr != baseSpeed) {
        ge.log(`自身最终时序 = (标准时序+属性时序增加值)*(1+时序增加系数) = (${baseSpeed}+${f0(sp)})*(1+${f2(asp)}) = ${f0(sr)}`)
    }
    const esp = enemyAtkDec + enemyDefDec + enemyAgiDec * 2
    if (esp != 0) {
        ge.log(`敌方时序属性降低值 = 攻击降低+防御降低+轻功降低*2 = ${f0(enemyAtkDec)}+${f0(enemyDefDec)}+${f0(enemyAgiDec)}*2 = ${f0(esp)}`)
    }
    const esr = (enemyBaseSpeed - esp) * (1 - enemyAsp)
    ge.log(`敌方最终时序 = (标准时序-属性时序降低值)*(1-时序降低系数) = (${enemyBaseSpeed}-${f0(esp)})*(1-${f2(enemyAsp)}) = ${f0(esr)}`)
    const totalSpeedRate = sr / esr / (baseSpeed / enemyBaseSpeed)
    ge.log(`最终时序倍率 = (自身最终时序/标准时序)/(敌方最终时序/标准敌方时序) = (${f0(sr)}/${baseSpeed})/(${f0(esr)}/${enemyBaseSpeed}) = ${f2(totalSpeedRate)}`)
    const gp = (totalSpeedRate - 1) * speedFactor
    ge.log(`时序分值 = (最终时序倍率-1)*时序分值系数 = (${f2(totalSpeedRate)}-1)*${speedFactor} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function calcClassesAtks(ae, ge) {
    // 计算攻击属性
    const sa = ae.getValueInc('裸攻')
    const ba = ae.getValueInc('场攻')
    const csa = gc.constants['标准裸攻']
    const cba = gc.constants['标准场攻']
    const atk = sa * csa + ba * cba
    if (sa != 0 || ba != 0) {
        ge.log(`属性：自身攻击加成${f2(sa)}，战场攻击加成${f2(ba)}；常数：标准自身攻击${f0(csa)}，标准战场攻击${f0(cba)}`)
        ge.log(`计算：总攻击增加 = 自身攻击加成*标准自身攻击+战场攻击加成*标准战场攻击 = ${f2(sa)}*${csa}+${f2(ba)}*${cba} = ${f0(atk)}`)
    }

    // 计算降低敌人防御
    const red = -ae.getValueInc('敌防')
    const ced = gc.constants['标准敌方场防']
    const enemyDefDec = red * ced
    if (red != 0) {
        ge.log(`属性：敌方防御减成：${f2(red)}，常数：标准敌方防御${ced}`)
        ge.log(`计算：敌方防御降低 = 敌方防御减成*标准敌方防御 = ${f2(red)}*${ced} = ${f2(enemyDefDec)}`)
    }

    //计算威力属性
    const pp = ae.getValueInc('威力')
    const cpp = gc.constants['标准武功威力']
    const power = pp * cpp
    if (pp != 0) {
        ge.log(`属性：全系威力加成${f2(pp)}；常数：标准威力攻击当量${cpp}`)
        ge.log(`计算：全系威力攻击增加 = 全系威力加成*标准威力攻击当量 = ${f2(pp)}*${cpp} = ${f0(power)}`)
    }
    // 计算主系威力
    const mpp = ae.getMainClassValueInc('威力')
    const mainPower = mpp * cpp
    if (mpp > 0) {
        ge.log(`属性：主系威力加成${f2(mpp)}；常数：标准威力攻击当量${cpp}`)
        ge.log(`计算：主系威力攻击增加 = 主系威力加成*标准威力攻击当量 = ${f2(mpp)}*${cpp} = ${f0(mainPower)}`)
    }

    // 计算系数攻击
    const ss = ae.getValue('裸系')
    const bs = ae.getValue('场系')
    const as = ae.getValue('攻系')
    const mas = ae.getMainClassValue('攻系')
    const cssm = gc.constants['标准裸主系']
    const cbsm = gc.constants['标准场主系']
    const csv = gc.constants['标准副系']
    const msAtk = (ss * cssm + (bs + as + mas) * cbsm) * 1.5
    const vsAtk = (ss * csv + bs * csv + as * csv) * 1.5
    if (ss != 0 || bs != 0 || as != 0 || mas != 0) {
        ge.log(`属性：自身系数加成${f2(ss)}，战场系数加成${f2(bs)}，攻防系数加成${f2(as)}，主系攻防系数加成${f2(mas)}；常数：标准主系自身数值${f0(cssm)}，标准主系战场数值${f0(cbsm)}，标准副系数值${f0(csv)}`)
        ge.log(`计算：主系攻击增加 = (自身系数加成*标准主系自身数值+(战场系数加成+攻防系数加成+主系攻防系数加成)*标准主系战场数值)*1.5 = (${f2(ss)}*${f0(cssm)}+(${f2(bs)}+${f2(as)}+${f2(mas)})*${f0(cbsm)})*1.5 = ${f0(msAtk)}`)
        ge.log(`计算：副系攻击增加 = (自身系数加成*标准副系数值+(战场系数加成+攻防系数加成)*标准副系数值)*1.5 = (${f2(ss)}*${f0(csv)}+(${f2(bs)}+${f2(as)})*${f0(csv)})*1.5 = ${f0(vsAtk)}`)
    }

    // 计算总攻击
    const totalAtkMain = atk + enemyDefDec*2 + power + mainPower + msAtk
    const totalAtkVice = atk + enemyDefDec*2 + power + vsAtk
    ge.log(`总攻击公式：总攻击 = 属性攻击+威力攻击+系数攻击+敌方防御降低*2`)
    ge.log(`主系总攻击 = ${f0(atk)}+(${f0(power)}+${f0(mainPower)})+${f0(msAtk)}+${f0(enemyDefDec)}*2 = ${f0(totalAtkMain)}`)
    ge.log(`副系总攻击 = ${f0(atk)}+${f0(power)}+${f0(vsAtk)}+${f0(enemyDefDec)}*2 = ${f0(totalAtkVice)}`)
    return [totalAtkMain, totalAtkVice]

    // const spep = gc.classes.map(cls => ae.getClassValueInc(cls, '威力'))
    // const spepFlag = spep.reduce((a, b) => a != 0 || b != 0, false)
    // const spePower = spep.map(p => p * cpp)
    // if (spepFlag) {
    //     ge.log(`各系威力加成属性：${spep.join(',')}；标准威力攻击常量${cpp}`)
    //     ge.log(`各系威力增幅：${spePower.map(p => p.toFixed(2)).join(',')}`)
    // }
    //
    // const atks = gc.classes.map((value, index) => {
    //     return atk + power + spePower[index] - enemyDefDec * 2
    // })
    // // 排序确定主系
    // atks.sort((a, b) => b - a)
    //
    // // 计算系数攻击
    // atks[0] += (gc.constants['标准场主系'] * ae.getValueInc('场系') + gc.constants['标准裸主系'] * ae.getValueInc('裸系')) * 1.5
    // for (let i = 1; i < atks.length; i++) {
    //     atks[i] += (gc.constants['标准副系'] * ae.getValueInc('场系') + gc.constants['标准副系'] * ae.getValueInc('裸系')) * 1.5
    // }
    // return atks
}

