function atkGradeEntry(ae) {
    const ge = new GradeEntry('基本攻击分')
    // 计算主副系攻击数值
    const [mainAtk, viceAtk] = calcClassesAtks(ae, ge)
    const expFactor = gc.constants['破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    const mainWeight = gc.constants['主系分值权重']
    ge.log(`分值公式：分值 = 攻击*(1+惩罚系数*攻击)*分值系数*主副系占比；常数：惩罚系数${expFactor}，分值系数${gradeFactor}，主系权重${mainWeight}`)
    const mp = calcPowerGrade(mainAtk, expFactor, gradeFactor) * mainWeight
    const vp = calcPowerGrade(viceAtk, expFactor, gradeFactor) * (1 - mainWeight)
    ge.log(`主系分值 = ${f0(mainAtk)}*(1+${expFactor}*${f0(mainAtk)})*${gradeFactor}*${f2(mainWeight)} = ${f2(mp)}`)
    ge.log(`副系分值 = ${f0(viceAtk)}*(1+${expFactor}*${f0(viceAtk)})*${gradeFactor}*${f2(1 - mainWeight)} = ${f2(vp)}`)
    ge.gradePoint = mp + vp
    ge.log(`总分值 = ${f2(mp)}+${f2(vp)} = ${f2(ge.gradePoint)}`)
    return ge
}

function calcClassesDefs(ae, ge) {
    // 计算防御属性
    const sd = ae.getValueInc('裸防')
    const bd = ae.getValueInc('场防')
    const csd = gc.constants['标准裸防']
    const cbd = gc.constants['标准场防']
    const def = sd * csd + bd * cbd
    if (sd != 0 || bd != 0) {
        ge.log(`属性：自身防御加成${f2(sd)}，战场防御加成${f2(bd)}；常数：标准自身防御${f0(csd)}，标准战场防御${f0(cbd)}`)
        ge.log(`计算：总防御增加 = 自身防御加成*标准自身防御+战场防御加成*标准战场防御 = ${f2(sd)}*${csd}+${f2(bd)}*${cbd} = ${f0(def)}`)
    }

    // 计算降低敌人攻击
    const rea = -ae.getValueInc('敌攻')
    const cea = gc.constants['标准敌方场攻']
    const enemyAtkDec = rea * cea
    if (rea != 0) {
        ge.log(`属性：敌方攻击减成：${f2(rea)}，常数：标准敌方攻击${cea}`)
        ge.log(`计算：敌方攻击降低 = 敌方攻击减成*标准敌方攻击 = ${f2(rea)}*${cea} = ${f2(enemyAtkDec)}`)
    }

    //计算威力属性
    const pp = -ae.getValueInc('减威')
    const cpp = gc.constants['标准武功威力']
    const power = pp * cpp
    if (pp != 0) {
        ge.log(`属性：全系威力减成${f2(pp)}；常数：标准威力攻击当量${cpp}`)
        ge.log(`计算：全系减威防御增加 = 全系威力减成*标准威力攻击当量 = ${f2(pp)}*${cpp} = ${f0(power)}`)
    }
    // 计算主系威力
    const mpp = -ae.getMainClassValueInc('减威')
    const mainPower = mpp * cpp
    if (mpp != 0) {
        ge.log(`属性：主系威力减成${f2(mpp)}；常数：标准威力攻击当量${cpp}`)
        ge.log(`计算：主系减威防御增加 = 主系威力减成*标准威力攻击当量 = ${f2(mpp)}*${cpp} = ${f0(mainPower)}`)
    }

    // 计算系数防御
    const ss = ae.getValue('裸系')
    const bs = ae.getValue('场系')
    const as = ae.getValue('攻系')
    const mas = ae.getMainClassValue('攻系')
    const cssm = gc.constants['标准裸主系']
    const cbsm = gc.constants['标准场主系']
    const csv = gc.constants['标准副系']
    const msDef = (ss * cssm + bs * cbsm + (as + mas) * cbsm) * 1.5
    const vsDef = (ss * csv + bs * csv + as * csv) * 1.5
    if (ss != 0 || bs != 0 || as != 0 || mas != 0) {
        ge.log(`属性：自身系数加成${f2(ss)}，战场系数加成${f2(bs)}，攻防系数加成${f2(as)}，主系攻防系数加成${f2(mas)}；常数：标准主系自身数值${f0(cssm)}，标准主系战场数值${f0(cbsm)}，标准副系数值${f0(csv)}`)
        ge.log(`计算：主系防御增加 = (自身系数加成*标准主系自身数值+(战场系数加成+攻防系数加成+主系攻防系数加成)*标准主系战场数值)*1.5 = (${f2(ss)}*${f0(cssm)}+(${f2(bs)}+${f2(as)}+${f2(mas)})*${f0(cbsm)})*1.5 = ${f0(msDef)}`)
        ge.log(`计算：副系防御增加 = (自身系数加成*标准副系数值+(战场系数加成+攻防系数加成)*标准副系数值)*1.5 = (${f2(ss)}*${f0(csv)}+(${f2(bs)}+${f2(as)})*${f0(csv)})*1.5 = ${f0(vsDef)}`)
    }

    // 计算总防御
    const totalDefMain = def*2 + enemyAtkDec + power + mainPower + msDef
    const totalDefVice = def*2 + enemyAtkDec + power + vsDef
    ge.log(`总防御公式：总防御 = 属性防御*2+威力防御+系数防御+敌方攻击降低`)
    ge.log(`主系总防御 = ${f0(def)}*2+(${f0(power)}+${f0(mainPower)})+${f0(msDef)}+${f0(enemyAtkDec)} = ${f0(totalDefMain)}`)
    ge.log(`副系总防御 = ${f0(def)}*2+${f0(power)}+${f0(vsDef)}+${f0(enemyAtkDec)} = ${f0(totalDefVice)}`)
    return [totalDefMain, totalDefVice]
}

function defGradeEntry(ae) {
    const ge = new GradeEntry('基本防御分')
    const [mainDef, viceDef] = calcClassesDefs(ae, ge)
    const expFactor = gc.constants['减破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    const mainWeight = 0.2
    ge.log(`分值公式：分值 = 防御*(1+惩罚系数*防御)*分值系数*主副系占比；常数：惩罚系数${expFactor}，分值系数${gradeFactor}，主系权重${mainWeight}`)

    const mp = calcPowerGrade(mainDef, expFactor, gradeFactor) * mainWeight
    const vp = calcPowerGrade(viceDef, expFactor, gradeFactor) * (1 - mainWeight)
    ge.log(`主系分值 = ${f0(mainDef)}*(1+${expFactor}*${f0(mainDef)})*${gradeFactor}*${f2(mainWeight)} = ${f2(mp)}`)
    ge.log(`副系分值 = ${f0(viceDef)}*(1+${expFactor}*${f0(viceDef)})*${gradeFactor}*${f2(1 - mainWeight)} = ${f2(vp)}`)
    ge.gradePoint = mp + vp
    ge.log(`总分值 = ${f2(mp)}+${f2(vp)} = ${f2(ge.gradePoint)}`)
    return ge
}

function critGradeEntry(ae) {
    const ge = new GradeEntry('暴击分')

    const critRate = ae.getValue('暴击')
    // const pierceRate = ae.getValue('突防')
    // const brokeRate = ae.getValue('破甲')
    // const dissRate = ae.getValue('破军')
    // const ignoreRate = ae.getValue('无视武常')
    if (critRate == 0) {
        return null
    }

    const atks = calcClassesAtks(ae, {log: () => {}})
    ge.log(`攻击属性: 主系攻击${f0(atks[0])}，副系攻击${f0(atks[1])}`)

    const critPower = gc.constants['标准暴击威力']
    // const piercePower = gc.constants['标准突防威力']
    // const brokePower = gc.constants['标准破甲威力']
    // const dissPower = gc.constants['标准破军威力']
    const expFactor = gc.constants['破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    //const enemyWc = gc.constants['标准敌方武常']
    const mainWeight = gc.constants['主系分值权重']

    const atkGrades = atks.map(atk => calcPowerGrade(atk, expFactor, gradeFactor))
    ge.log(`攻击分值: 主系分值${f2(atkGrades[0])}，副系分值${f2(atkGrades[1])}`)

    let gp = 0.0
    const specGradeCalc = (name, sRate, sPower) => {
        //ge.log(`===计算${name}分值===`)
        ge.log(`计算${name}相关属性: ${name}率${f2(sRate)}；常数：标准${name}威力${f0(sPower)}`)
        const sAtks = atks.map(atk => atk + sPower)
        ge.log(`${name}时攻击威力 = 基本攻击威力+标准${name}威力；主系攻击 = ${f0(atks[0])}+${sPower} = ${f0(sAtks[0])}，副系攻击 = ${f0(atks[1])}+${sPower} = ${f0(sAtks[1])}`)
        const values = sAtks.map(atk => calcPowerGrade(atk, expFactor, gradeFactor))
        ge.log(`${name}时攻击分值计算：主系分值 = 计算攻击分值(${f0(sAtks[0])}) = ${f2(values[0])}，副系分值 = 计算攻击分值(${f0(sAtks[1])}) = ${f2(values[1])}`)
        const grades = values.map((value, index) => (value - atkGrades[index]) * sRate)
        ge.log(`${name}附加分值 = (${name}时攻击分值-基础攻击分值)*${name}率；主系攻击分值 = (${f2(values[0])}-${f2(atkGrades[0])})*${f2(sRate)}=${f2(grades[0])}，副系攻击分值 = (${f2(values[1])} - ${f2(atkGrades[1])})*${f2(sRate)} = ${f2(grades[1])}`)
        const sGp = grades[0] * mainWeight + grades[1] * (1 - mainWeight)
        ge.log(`综合${name}附加分值 = 主系攻击分值*主系分值权重+副系攻击分值*(1-主系分值权重) = ${f2(grades[0])}*${f2(mainWeight)}+${f2(grades[1])}*${f2(1 - mainWeight)} = ${f2(sGp)}`)
        gp += sGp
        return sGp
    }

    // 计算暴击
    const gpParts = []
    if (critRate > 0) {
        gpParts.push(['暴击分值', specGradeCalc('暴击', critRate, critPower)])
    }
    // // 计算突防
    // if (pierceRate > 0) {
    //     gpParts.push(['突防分值', specGradeCalc('突防', pierceRate, piercePower)])
    // }
    // // 计算破甲
    // if (brokeRate > 0) {
    //     gpParts.push(['破甲分值', specGradeCalc('破甲', brokeRate, brokePower)])
    // }
    // // 计算破军
    // if (dissRate > 0) {
    //     gpParts.push(['破军分值', specGradeCalc('破军', dissRate, dissPower)])
    // }
    // // 计算无视武常
    // if (ignoreRate > 0) {
    //     const ignorePower = enemyWc * 3
    //     gpParts.push(['无视武常分值', specGradeCalc('无视武常', ignoreRate, ignorePower)])
    // }
    ge.log(`情景攻击总分 = ${gpParts.map(([name, value]) => `${name}(${f2(value)})`).join(' + ')} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

