function atkSoftGradeEntry(ae) {
    const ge = new GradeEntry('增伤分')

    // 列举命中伤害
    const hitInc = ae.getValueInc('命中')
    const dmgInc = ae.getValueInc('伤害')
    const mh = ae.getMainClassValueInc('命中')
    const md = ae.getMainClassValueInc('伤害')
    ge.log(`属性：命中加成${f2(hitInc)}，伤害加成${f2(dmgInc)}, 主系命中加成${f2(mh)}, 主系伤害加成${f2(md)}`)

    // 计算轻功影响
    const agi = gc.getTotalAgi(ae)
    const enemyAgiDec = -gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    const speedHitStandard = gc.constants['轻功命中基准值']
    const agiHitInc = (agi + enemyAgiDec) / speedHitStandard
    if (agi != 0 || enemyAgiDec != 0) {
        ge.log(`属性计算：自身轻功增加 = ${f0(agi)}，敌方轻功降低 = ${f0(enemyAgiDec)}；常数：轻功命中基准值${speedHitStandard}`)
        ge.log(`轻功命中加成 = (1+(自身轻功增加+敌方轻功降低)/轻功命中基准值) = (1+(${f0(agi)}+${f0(enemyAgiDec)})/${speedHitStandard}) = ${f2(agiHitInc)}`)
    }

    const gradeFactor = gc.constants['命中伤害分值系数']
    const mainWeight = gc.constants['主系分值权重']
    const vp = (1 + hitInc) * (1 + agiHitInc) * (1 + dmgInc)
    const mp = vp * (1 + mh) * (1 + md)
    ge.log(`综合增伤 = (1+命中加成)*(1+轻功命中加成)*(1+伤害加成) = (1+${f2(hitInc)})*(1+${f2(agiHitInc)})*(1+${f2(dmgInc)}) = ${f2(vp)}`)
    ge.log(`主系增伤 = 综合增伤*(1+主系命中加成)*(1+主系伤害加成) = ${f2(vp)}*(1+${f2(mh)})*(1+${f2(md)}) = ${f2(mp)}，副系增伤 = 综合增伤 = ${f2(vp)}`)
    const mpf = mp >= 1 ? (mp - 1) : 1 - 1 / mp
    const vpf = vp >= 1 ? (vp - 1) : 1 - 1 / vp
    if (mp < 1) {
        ge.log(`主系增伤差值(修正) = 1-1/主系增伤 = 1-1/${f2(mp)} = ${f2(mpf)}`)
    } else {
        ge.log(`主系增伤差值 = 主系增伤-1 = ${f2(mp)}-1 = ${f2(mpf)}`)
    }
    if (vp < 1) {
        ge.log(`副系增伤差值(修正) = 1-1/副系增伤 = 1-1/${f2(vp)} = ${f2(vpf)}`)
    } else {
        ge.log(`副系增伤差值 = 副系增伤-1 = ${f2(vp)}-1 = ${f2(vpf)}`)
    }
    const mgp = mpf * gradeFactor * mainWeight
    const vgp = vpf * gradeFactor * (1 - mainWeight)
    const gp = mgp + vgp
    ge.log(`主系增伤分= 主系增伤差值*命中伤害分值系数*主系分值权重 = ${f2(mgp)}*${gradeFactor}*${mainWeight} = ${f2(mgp)}`)
    ge.log(`副系增伤分= 副系增伤差值*命中伤害分值系数*(1-主系分值权重) = ${f2(vgp)}*${gradeFactor}*(1-${mainWeight}) = ${f2(vgp)}`)
    ge.log(`总增伤分 = 主系增伤分+副系增伤分 = ${f2(mgp)}+${f2(vgp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function defSoftGradeEntry(ae) {
    const ge = new GradeEntry('减伤分')

    // 列举闪避减伤
    const hitDec = -ae.getValueInc('闪避')
    const dmgDec = -ae.getValueInc('减伤')
    const mh = -ae.getMainClassValueInc('闪避', true)
    const md = -ae.getMainClassValueInc('减伤', true)
    ge.log(`属性：闪避加成${f2(hitDec)}，减伤加成${f2(dmgDec)}, 主系闪避加成${f2(mh)}, 主系减伤加成${f2(md)}`)

    // 计算轻功影响
    const agi = gc.getTotalAgi(ae)
    const enemyAgiDec = -gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    const speedHitStandard = gc.constants['轻功命中基准值']
    const agiHitDec = (agi + enemyAgiDec) / speedHitStandard
    if (agi != 0 || enemyAgiDec != 0) {
        ge.log(`属性计算：自身轻功增加 = ${f0(agi)}，敌方轻功降低 = ${f0(enemyAgiDec)}；常数：轻功命中基准值${speedHitStandard}`)
        ge.log(`轻功闪避加成 = (1+(自身轻功增加+敌方轻功降低)/轻功命中基准值) = (1+(${f0(agi)}+${f0(enemyAgiDec)})/${speedHitStandard}) = ${f2(agiHitDec)}`)
    }

    // 特殊词条减伤期望值
    let specDmgDec = 1
    const specDmgs = []
    const immune = ae.getValue('不受击')
    if (immune > 0) {
        specDmgDec *= 1 - immune
        specDmgs.push({type: '不受攻击', value: f2(1 - immune)})
    }
    const half = ae.getValue('伤害减半')
    if (half > 0) {
        specDmgDec *= 1 - half / 2
        specDmgs.push({type: '伤害减半', value: f2(1 - half / 2)})
    }
    if (specDmgs.length > 0) {
        const specDmgDecStr = specDmgs.map(s => `${s.type}：${s.value}`).join('，')
        ge.log(`特殊减伤因素${specDmgDecStr}`)
        if (specDmgs.length > 1) {
            ge.log(`特殊减伤期望值 = ${specDmgs.map(s => s.value).join('*')} = ${f2(specDmgDec)}`)
        } else {
            ge.log(`特殊减伤期望值 = ${specDmgs[0].value}`)
        }
    }

    const gradeFactor = gc.constants['命中伤害分值系数']
    const mainWeight = 0.2
    const vp = (1 - hitDec) * (1 - agiHitDec) * (1 - dmgDec) * specDmgDec
    const mp = vp * (1 - mh) * (1 - md)
    ge.log(`综合减伤 = (1-闪避加成)*(1-轻功闪避加成)*(1-减伤加成)*特殊减伤期望 = (1-${f2(hitDec)})*(1-${f2(agiHitDec)})*(1-${f2(dmgDec)})*${f2(specDmgDec)} = ${f2(vp)}`)
    ge.log(`主系减伤 = 综合减伤*(1-主系闪避加成)*(1-主系减伤加成) = ${f2(vp)}*(1-${f2(mh)})*(1-${f2(md)}) = ${f2(mp)}，副系减伤 = 综合减伤 = ${f2(vp)}`)
    const mpf = mp >= 1 ? 1 - mp : 1 / mp - 1
    const vpf = vp >= 1 ? 1 - mp : 1 / vp - 1
    if (mp < 1) {
        ge.log(`主系减伤差值(修正) = 1/主系减伤-1 = 1/${f2(mp)}-1 = ${f2(mpf)}`)
    } else {
        ge.log(`主系减伤差值(修正) = 1-主系减伤 = 1-${f2(mp)} = ${f2(mpf)}`)
    }
    if (vp < 1) {
        ge.log(`副系减伤差值(修正) = 1/副系增伤-1 = 1/${f2(vp)}-1 = ${f2(vpf)}`)
    } else {
        ge.log(`副系减伤差值(修正) = 1-副系减伤 = 1-${f2(vp)} = ${f2(vpf)}`)
    }
    const mgp = mpf * gradeFactor * mainWeight
    const vgp = vpf * gradeFactor * (1 - mainWeight)
    const gp = mgp + vgp
    ge.log(`主系减伤分= 主系减伤差值*命中伤害分值系数*主系分值权重 = ${f2(mgp)}*${gradeFactor}*${mainWeight} = ${f2(mgp)}`)
    ge.log(`副系减伤分= 副系减伤差值*命中伤害分值系数*(1-主系分值权重) = ${f2(vgp)}*${gradeFactor}*(1-${mainWeight}) = ${f2(vgp)}`)
    ge.log(`总减伤分 = 主系减伤分+副系减伤分 = ${f2(mgp)}+${f2(vgp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

