function injuryGradeEntry(ae) {
    const ge = new GradeEntry('受伤分')
    const injury = ae.getValueInc('加受伤')
    let injuried = -ae.getValueInc('减受伤')
    if (injury == 0 && injuried == 0) {
        return null
    }
    if (injuried > 1) {
        injuried = 1
    }
    const injuryFactor = gc.constants['攻击受伤度分值系数']
    const injuriedFactor = gc.constants['被攻击受伤度分值系数']
    ge.log(`属性：攻击受伤度${f2(injury)}，被攻击受伤度${f2(injuried)}，常数：攻击受伤度分值系数${injuryFactor}，被攻击受伤度分值系数${injuriedFactor}`)

    const injuryGp = injury > 0 ? calcSquareGrade(injury, injuryFactor) : -calcSquareGrade(injury, injuriedFactor)
    if (injury > 0) {
        ge.log(`攻击受伤度分 = 攻击受伤度^2*攻击受伤度分值系数 = ${f2(injury)}^2*${injuryFactor} = ${f2(injuryGp)}`)
    } else if(injury < 0) {
        ge.log(`攻击受伤度分 = -攻击受伤度^2*被攻击受伤度分值系数 = ${f2(injury)}^2*${injuriedFactor} = ${f2(injuryGp)}`)
    }
    const injuriedGp = injuried > 0 ? calcSquareGrade(injuried, injuriedFactor) : -calcSquareGrade(injuried, injuryFactor)
    if (injuried > 0) {
        ge.log(`被攻击受伤度分 = 被攻击受伤度^2*被攻击受伤度分值系数 = ${f2(injuried)}^2*${injuriedFactor} = ${f2(injuriedGp)}`)
    } else if (injuried < 0) {
        ge.log(`被攻击受伤度分 = -被攻击受伤度^2*攻击受伤度分值系数 = -${f2(injuried)}^2*${injuryFactor} = ${f2(injuriedGp)}`)
    }
    const gp = injuryGp + injuriedGp
    ge.log(`受伤分 = 攻击受伤度分+被攻击受伤度分 = ${f2(injuryGp)}+${f2(injuriedGp)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function counterGradeEntry(ae) {
    const ge = new GradeEntry('反伤分')
    const counter = ae.getValue('反弹')
    const reflect = ae.getValue('反震')
    const transfer = ae.getValue('挪移')
    if (counter == 0 && reflect == 0 && transfer == 0) {
        return null
    }
    const counterFactor = gc.constants['伤害反弹分值系数']
    const reflectFactor = gc.constants['伤害反震分值系数']
    const transferFactor = gc.constants['伤害转移分值系数']
    ge.log(`属性：反弹率${f2(counter)}，反震率${f2(reflect)}，挪移率${f2(transfer)}，常数：反弹分值系数${counterFactor}，反震分值系数${reflectFactor}，挪移分值系数${transferFactor}`)

    const counterGrade = calcLinearGrade(counter, 0, counterFactor)
    const reflectGrade = calcLinearGrade(reflect, 0, reflectFactor)
    const transferGrade = calcLinearGrade(transfer, 0, transferFactor)
    ge.log(`反弹分 = 反弹率*反弹分值系数 = ${f2(counter)}*${counterFactor} = ${f2(counterGrade)}`)
    ge.log(`反震分 = 反震率*反震分值系数 = ${f2(reflect)}*${reflectFactor} = ${f2(reflectGrade)}`)
    ge.log(`挪移分 = 挪移率*转移分值系数 = ${f2(transfer)}*${transferFactor} = ${f2(transferGrade)}`)
    const gp = counterGrade + reflectGrade + transferGrade
    ge.log(`反伤分 = 反弹分+反震分+挪移分 = ${f2(counterGrade)}+${f2(reflectGrade)}+${f2(transferGrade)} = ${f2(gp)}`)
    ge.gradePoint = gp
    return ge
}

function immuneGradeEntry(ae) {
    const ge = new GradeEntry('免疫分')
    if (ae.immunes.length == 0) {
        return null
    }
    let gp = 0.0
    const immunes = []
    Object.entries(gc.immuneConf).forEach(([key, value]) => {
        if (ae.immunes[key]) {
            gp += value
            immunes.push({key: key, value: value})
        }
    })
    ge.log(`免疫条目：${immunes.map(i => `${i.key}(${i.value})`).join('，')}`)
    if (immunes.length > 1) {
        ge.log(`免疫分 = ${immunes.map(i => i.value).join('+')} = ${f2(gp)}`)
    } else {
        ge.log(`免疫分 = ${f2(gp)}`)
    }
    ge.gradePoint = gp
    return ge
}

function debuffGradeEntry(ae) {
    const ge = new GradeEntry('异常分')
    if (ae.deBuffMakes.length == 0) {
        return null
    }
    let gp = 0
    const debuffs = []
    Object.entries(gc.debuffConf).forEach(([key, conf]) => {
        const dms = ae.deBuffMakes[key]
        if (dms == null) {
            return
        }
        dms.forEach((dm) => {
            const opt = {}
            if (key == '点穴') {
                opt['square'] = true
            }
            const p = calcDebuffGrade(dm.rate/100, dm.round, gc.constants['异常概率基础值'], conf, opt)
            gp += p
            debuffs.push([key, dm.rate, `${dm.round}回合`, p])
        })
    })
    if (ae.deBuffMakes['击退']) {
        const dms = ae.deBuffMakes['击退']
        dms.forEach((dm) => {
            const factor = (100 / (100 - dm.round) - 1) * 10
            // const idx = gc.repelConf.findIndex(conf => conf[0] > dm.round) - 1
            // const conf = gc.repelConf[idx]
            const p = factor * dm.rate/100
            gp += p
            debuffs.push(['击退', dm.rate, `击退${dm.round}%行动条`, p])
        })
    }
    ge.log(`异常条目：${debuffs.map(d => `${d[0]}#${d[1]}%${d[2]}(${f2(d[3])})`).join('，')}`)
    if (debuffs.length > 1) {
        ge.log(`异常分 = ${debuffs.map(d => f2(d[3])).join('+')} = ${f2(gp)}`)
    } else {
        ge.log(`异常分 = ${f2(gp)}`)
    }
    ge.gradePoint = gp
    return ge
}

function extGradeEntry(ae) {
    const ge = new GradeEntry('特殊词条分')
    const entries = ae.getExtValues()
    if (entries.length == 0) {
        return null
    }
    let gp = 0.0
    const exts = []
    entries.forEach(entry => {
        const key = entry.attrType
        const conf = AttrPrefixMap[key]
        const value = entry.value
        const rate = entry.rate
        const r = conf.calcGrade(value, rate)
        // 计算条件
        const cond = entry.condition
        if (cond) {
            const condConf = CONDITIONS[cond]
            if (condConf) {
                const factor = condConf.factor
                r.res *= factor
                r.cond = {name: cond, factor: factor}
            }
        }
        exts.push({key, value, rate, r})
        gp += r.res
    })
    exts.forEach((e) => {
        let p1 = `[${e.key}]`
        if (e.r.cond) {
            p1 += `(${e.r.cond.name})`
        }
        p1 += `${f2(e.r.res)}`

        let p2 = `数值${f2(e.value)}，系数${e.r.factor}`
        if (e.r.base != 0) {
            p2 += `，基数${e.r.base}`
        }
        if (e.r.cond) {
            p2 += `，条件系数${e.r.cond.factor}`
        }
        if (e.rate) {
            p2 += `，概率${e.rate}`
        }

        let p3 = `${f2(e.value)}*${f2(e.r.factor)}`
        if (e.r.base != 0) {
            p3 += `+${f2(e.r.base)}`
        }
        if (e.r.base != 0 && (e.r.cond || e.rate)) {
            p3 = `(${p3})`
        }
        if (e.r.cond) {
            p3 += `*${e.r.cond.factor}`
        }
        if (e.rate) {
            p3 += `*${e.rate}`
        }
        p3 = `分值 = ${p3} = ${f2(e.r.res)}`

        ge.log(`${p1}：${p2}；${p3}`)
    })
    ge.gradePoint = gp
    return ge
}

function calcPowerGrade(power, expFactor, gradeFactor) {
    return power * (1 + power * expFactor) * gradeFactor
}

function calcLinearGrade(value, base, factor) {
    return base + value * factor
}

function calcSquareGrade(value, factor) {
    return value * value * factor
}

function calcCastGrade(value, confs, getter) {
    const intValue = Math.round(value * 100)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    if (index < -1) {
        index = confs.length - 1
    }
    return getter(confs[index])
}

function calcRangeGrade(value, confs, getter) {
    const intValue = Math.round(value)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    let gp = getter(confs[0])
    let pre = 0.0
    if (index < -1) {
        index = confs.length - 1
    }
    for (let i = 0; i < index; i++) {
        gp += (confs[i][0] - pre) * getter(confs[i])
        pre = confs[i][0]
    }
    gp += (intValue - pre) * getter(confs[index])
    if (gp > getter(confs[0]) * 20) {
        gp = getter(confs[0]) * 20
    }
    return gp
}

function calcDebuffGrade(rate, round, base, factor, option) {
    if (rate == 0 || round == 0) {
        return 0
    }
    let fixedRate = rate + base
    if (fixedRate > 1) {
        fixedRate = 1
    }
    if (option && option['square']) {
        fixedRate *= fixedRate
    }
    return fixedRate * round * factor
}