<template>
  <div>
    <TabDataPage
        :name="'event-group'"
        :tabs="tabs"
        :headersSource="headers"
        :itemsSource="items"
        :header-labels="headerLabels"
        :mapper="convertValue"
        @tabChange="selectTab"
    />
  </div>
</template>

<script>
import {convertValue, headers, headerLabels, tabs} from "@/config/event-group-config";
import TabDataPage from "@/components/TabDataPage";

export default {
  name: "EventGroupView",
  data() {
    return {
      selectType: 'xw',
      tabs,
      headerLabels,
      convertValue,
    };
  },
  methods: {
    selectTab(value) {
      this.selectType = value;
    },
  },
  computed: {
    headers() {
      return headers
    },
    items() {
      return this.$store.state.EventSource[this.selectType]
          ? this.$store.state.EventSource[this.selectType].map(event => {
            return {
              ...event,
              tabGroup: this.selectType,
            }
          })
          : []
    }
  },
  components: {
    TabDataPage,
  }
}
</script>

