<template>
  <v-container fluid grid-list-md style="width: 95%; min-width: 95%; overflow: auto" >
    <DataPage
        :name="'battle'"
        :headers-source="headers"
        :items-source="items"
        :mapper="mapper"
        :header-labels="headerLabels"

        :extra-filters="[]"
    >
      <template v-slot:options>
        <v-card flat>
          <v-card-title class="text-subtitle-2">
            阵容显示方式
          </v-card-title>
          <v-card-text>
            <v-chip-group
                column
                multiple
                active-class="primary--text text--accent-4"
                v-model="showStyle"
            >
              <v-chip outlined >
                编号
              </v-chip>
              <v-chip outlined >
                等级
              </v-chip>
            </v-chip-group>
          </v-card-text>
        </v-card>
      </template>
    </DataPage>
  </v-container>
</template>

<script>
import DataPage from "@/components/DataPage";
import {convertValue, headers, headerLabels,} from "@/config/battle-config";
export default {
  name: "BattleView",
  data() {
    return {
      headerLabels,
      showStyle: [0],
      sortType: 0,
    };
  },
  computed: {
    headers() {
      return headers;
    },
    items() {
      return Object.values(this.$store.state.BattleSource)
    },
    mapper() {
      return item => convertValue(item, {showStyle: this.showStyle, sortType: this.sortType})
    }
  },
  components: {
    DataPage,
  }
}
</script>

