import GradeConfig from './grade-config'

function valueRateCalculation(factor, base) {
    base = base || 0;
    return (value, rate) => {
        rate = rate || 1
        const res = (base + value * factor) * rate;
        return {res, factor, base}
    }
}

function linearCalculation(factor, base) {
    base = base || 0;
    return value => {
        const res = base + value * factor;
        return {res, factor, base}
    }
}

function linearCalculationPct(factor) {
    return value => linearCalculation(factor)(value - 1)
}

function squareCalculation(factor, base) {
    base = base || 0;
    return value => {
        const res = base + value * value * factor;
        return {res, factor, base}
    }
}

export function inverseCalculation(factor) {
    return value => {
        if (value < 0) {
            const res = linearCalculation(factor, 0)(-value)
            res.res = -res.res
            return res
        }

        const v0 = 1 / (1 - value) - 1
        const res = v0 * factor
        return {res, factor, base: 0}
    }
}

function inverseCalculationPct(factor) {
    return value => inverseCalculation(factor)(1 - value)
}

function printAttr(options) {
    options = options || {};
    return (attr, value, rate) => {
        if (options.flag && value == 1) {
            return attr;
        }
        if (!options['num'] || options['pct'] == true) {
            value = `${(value * 100).toFixed(0)}%`;
        }
        if (options['rate']) {
            rate = rate || 1;
            value = `${value}:${(rate * 100).toFixed(0)}%`;
        }
        const symbol = options['symbol'] || '+';
        return `${attr}${symbol}${value}`;
    };
}

function printDebuff(options) {
    if (options['round']) {
        return (attr, value, rate) => {
            return `${attr}${value.toFixed(0)}%+${(rate * 100).toFixed(0)}%`
        }
    }
    return (attr, value, rate) => `${attr}${(value * 100).toFixed(0)}%+${(rate * 100).toFixed(0)}%`
}

export class AttrPrefix {
    constructor(key, group, options) {
        options = options || {};
        this.key = key
        this.group = group
        this.name = options.name || key;
        this.value = options.value || 0;
        this.desc = options.desc || '';
        this.calcType = options.calcType || 'valueRate';
        this.calcFlags = options.calcFlags || [];
        this.calcArgs = options.calcArgs || {};
        this.printType = options.printType || 'attr';
        this.printFlags = options.printFlags || [];
        this.printOptions = options.printOptions || {};
    }

    hasCalcFlag(flag) {
        return this.calcFlags.indexOf(flag) >= 0;
    }

    hasPrintFlag(flag) {
        return this.printFlags.indexOf(flag) >= 0;
    }

    calcTypeDisplay() {
        const display = []
        // if (this.hasCalcFlag('cmn')) {
        //     display.push('公式')
        // }
        switch (this.calcType) {
            case 'inverse':
                display.push('反比计算')
                break
            case 'inversePct':
                display.push('反比计算')
                break
        }
        return display.join('|')
    }

    calcGrade(value, rate) {
        switch (this.calcType) {
            case 'linear':
                return linearCalculation(this.value, this.calcArgs['base'])(value);
            case 'inverse':
                return inverseCalculation(this.value)(value)
            case 'square':
                return squareCalculation(this.value, this.calcArgs['base'])(value);
            case 'linearPct':
                return linearCalculationPct(this.value)(value)
            case 'inversePct':
                return inverseCalculationPct(this.value)(value)
            case 'valueRate':
                return valueRateCalculation(this.value, this.calcArgs['base'])(value, rate);
        }
        throw new Error(`Unknown calcType: ${this.calcType}`);
    }

    print(attr, value, rate) {
        const options = {
            ...this.printOptions
        }
        this.printFlags.forEach(flag => {
            options[flag] = true;
        })
        switch (this.printType) {
            case 'attr':
                return printAttr(options)(attr, value, rate);
            case 'debuff':
                return printDebuff(options)(attr, value, rate);
        }
        throw new Error(`Unknown printType: ${this.printType}`);
    }
}

function ap(key, group, options) {
    if (group instanceof Object) {
        options = group;
        group = null;
    }
    return new AttrPrefix(key, group, options);
}

