export class AttrEntries {
    constructor() {
        this.valueAttrs = {}
        this.classValueAttrs = {}
        this.extValues = []
        this.flags = {}
        this.immunes = {}
        this.deBuffMakes = {}
    }

    // functions
    isEmpty() {
        return Object.keys(this.valueAttrs).length === 0
            && Object.keys(this.classValueAttrs).length === 0
            && this.extValues.length === 0
            && Object.keys(this.flags).length === 0
            && Object.keys(this.immunes).length === 0
            && Object.keys(this.deBuffMakes).length === 0
    }

    getValue(attrType) {
        return this.valueAttrs[attrType] || 0
    }

    setValue(attrType, value) {
        this.valueAttrs[attrType] = value
    }

    getValueInc(attrType) {
        const val = this.getValue(attrType)
        return val != 0 ? val - 1 : 0
    }

    getClassValue(clsType, attrType) {
        return this.classValueAttrs[clsType] && this.classValueAttrs[clsType][attrType] || 0
    }

    getMainClassValue(attrType) {
        const func = Math.max
        return func(...GradeConfig.classes.map(cls => this.getClassValue(cls, attrType)))
    }

    setClassValue(clsType, attrType, value) {
        if (!this.classValueAttrs[clsType]) {
            this.classValueAttrs[clsType] = {}
        }
        this.classValueAttrs[clsType][attrType] = value
    }

    getClassValueInc(clsType, attrType) {
        const val = this.getClassValue(clsType, attrType)
        return val != 0 ? val - 1 : 0
    }

    getMainClassValueInc(attrType, negative) {
        const func = negative ? Math.min : Math.max
        return func(...GradeConfig.classes.map(cls => this.getClassValueInc(cls, attrType)))
    }

    getExtValues() {
        return this.extValues
    }

    setExtValue(attrType, value, rate, condition) {
        const extValue = {attrType, value, rate, condition}
        this.extValues.push(extValue)
    }

    addDeBuffMake(type, rate, round) {
        if (!this.deBuffMakes[type]) {
            this.deBuffMakes[type] = []
        }
        this.deBuffMakes[type].push({rate, round})
    }

    addImmune(type) {
        this.immunes[type] = true
    }
}