import {searchCustomSkill, searchSkill, searchTitle} from "@/utils/SourceDataUtil";
import store from "@/store";

const banRules = {
    title: {
        2: ['1', '270', '6', '72', '15', '134', '379', '439', '235', '399', '222', '109'],
        4: [],
    },
    talent: {
        2: ['1053', '807', '792', '975', '781', '800', '794', '797', '785', '830', '786', '1047', '806', '805'],
        4: ['848', '796', '789'],
    },
    effect: {
        2: ['1089', '184', '888', '1009', '891', '901'],
        4: ['885', '642', '1090', '620', '908'],
    },
}

export class Role {
    constructor(role) {
        this.base = {
            name: '新角色',
            id: 0,
            round: 4,
            ke: 1,
            classType: '无',
        }
        this.config = {
            title: null,
            talent: null,
            effect: null,
            triggerSkill: null,
            twoHands: false,
            harmony: false,
        }
        this.options = {
            titleCustom: false,
            talentCustom: false,
            effectCustom: false,
            triggerCustom: false,
            titleScoreOnly: false,
            titleDescShow: true,
            talentKeOnly: false,
            talentDescShow: true,
            effectKeOnly: false,
            effectDescShow: true,
            triggerKeOnly: false,
            triggerDescShow: true,
        }
        if (role) {
            Object.assign(this.base, role.base)
            Object.assign(this.config, role.config)
            Object.assign(this.options, role.options)
        }
    }
    checkTotalGrade(grade) {
        return checkTotalGrade(this, grade)
    }
    checkBan(type, skillId) {
        //将skillId转换成string
        skillId = skillId.toString()
        const banRule = banRules[type]
        if (this.base.round <= 4) {
            if (banRule[4].includes(skillId)) {
                return `4周以下禁用`
            }
        }
        if (this.base.round <= 2) {
            if (banRule[2].includes(skillId)) {
                return `2周禁用`
            }
        }
        return ''
    }
    getTitle() {
        const nil = {
            id: -1,
            name: '无',
            desc: '无',
            grade: 0,
        }
        if (!this.config.title) {
            return nil
        }
        const id = this.config.title.split(':')[1]
        const title = searchTitle(id)
        if (!title) {
            return nil
        }
        return {
            id: id,
            name: title.name,
            desc: title.desc,
            grade: title.grade,
        }
    }
    getSkill(key) {
        const nil = {
            id: -1,
            name: '无',
            desc: '无',
            grade: 0,
        }
        if (!key) {
            return nil
        }
        const [type, id] = key.split(':')
        if (type == 'std') {
            const skill = searchSkill(id)
            if (!skill) {
                return nil
            }
            return {
                id: skill.id,
                name: skill.name,
                desc: skill.type == 7 ? skill['text_equip'] : skill['text_use'],
                grade: fixSkillGrade(this, skill, Number(skill.grade), false),
                grade_detail: checkSkillGrade(this, Number(skill.grade)),
            }
        } else if (type == 'custom') {
            const customSkill = searchCustomSkill(id)
            if (!customSkill) {
                return nil
            }
            const grade = customSkill.skill.calcGrade()
            return {
                id: customSkill.id,
                name: customSkill.name,
                desc: grade.desc,
                grade: fixSkillGrade(this, customSkill.skill, grade.totalGrade, true),
                grade_detail: checkSkillGrade(this, grade.totalGrade),
            }
        }
    }
}

function fixSkillGrade(role, skill, grade, custom) {
    if (role.base.round < 6) {
        if ((custom && skill.base.type === '特技') || (!custom && skill.type === 8)) {
            if (grade > 10 && grade <= 15) {
                return 10
            }
        }
    }
    return grade
}

function getMaxSkillGrade(role) {
    // 规则：
    // 6周，最高1000分
    // 4周单氪，最高25分
    // 4周双氪以上，最高30分
    // 2周单氪，最高12.5分
    // 2周双氪以上，最高15分
    let maxGrade = 1000
    if (role.base.round <= 2) {
        if (role.base.ke > 1) {
            maxGrade = 7.5
        } else {
            maxGrade = 5
        }
    } else if (role.base.round <= 4) {
        if (role.base.ke > 1) {
            maxGrade = 15
        } else {
            maxGrade = 12.5
        }
    }
    return maxGrade
}

function checkSkillGrade(role, grade) {
    // 规则：
    // 6周，最高1000分
    // 4周单氪，最高12.5分
    // 4周双氪以上，最高15分
    // 2周单氪，最高5分
    // 2周双氪以上，最高7.5分
    let maxGrade = getMaxSkillGrade(role)
    if (grade > maxGrade) {
        return `上限${maxGrade}分`
    }
    return ''
}

function checkTotalGrade(role, grade) {
    // 规则：
    // 6周，最高1000分
    // 4周单氪，最高25分
    // 4周双氪以上，最高30分
    // 2周单氪，最高12.5分
    // 2周双氪以上，最高15分
    let maxGrade = 1000
    if (role.base.round <= 2) {
        if (role.base.ke > 1) {
            maxGrade = 15
        } else {
            maxGrade = 12.5
        }
    } else if (role.base.round <= 4) {
        if (role.base.ke > 1) {
            maxGrade = 30
        } else {
            maxGrade = 25
        }
    }
    if (grade > maxGrade) {
        return `上限${maxGrade}分`
    }
    return ''
}

export function getTitleList() {
    if (!store.getters['isReady']) {
        return []
    }
    return store.state.TitleConfigSource
        .map((item) => {
            let text = `[${item.skillId}]${item.name}(${item.grade || 0})`
            if (item.ext_grade) {
                text += `#${item.ext_grade.desc}`
            }
            return {
                'text': text,
                'value': `std:${item.skillId}`,
                'grade': item.grade || 0,
            }
        })
        .sort((a, b) => b.grade - a.grade)
}

export function getTalentList(role) {
    const custom = role.options.talentCustom
    if (!store.getters['isReady']) {
        return []
    }
    const maxGrade = getMaxSkillGrade(role)
    if (custom) {
        return store.state['customSkill'].customSKills
            .filter((item) => item.skill.base.type === '天赋')
            .map((item) => {
                const grade = Number(item.skill.calcGrade().totalGrade.toFixed(2))
                return {
                    'text': `${item.name}(${grade})`,
                    'value': `custom:${item.name}`,
                    'ke': true,
                    'grade': grade,
                }
            })
            .filter((item) => item.grade <= maxGrade)
            .sort((a, b) => b.grade - a.grade)
    }
    return Object.values(store.state.SkillSource)
        .filter((item) => item.type === 7)
        .map((item) => {
            return {
                'text': `[${item.id}]${item.name}(${item.grade})`,
                'value': `std:${item.id}`,
                'ke': item.ke == '1',
                'grade': item.grade,
            }
        })
        .filter((item) => item.grade <= maxGrade)
        .sort((a, b) => b.grade - a.grade)
}

