export function getTriggerSkillList(role) {
    if (!store.getters['isReady']) {
        return []
    }
    const maxGrade = getMaxSkillGrade(role)
    return Object.values(store.state.SkillSource)
        .filter((item) => item.type >= 0 && item.type <= 4)
        .filter((item) => item['trigger_effect'])
        .map((item) => {
            const triggerSkillId = item['trigger_effect'][0]
            const triggerSkill = searchSkill(triggerSkillId)
            return {
                'text': `[${item.id}]${item.name}：${triggerSkill.name}(${triggerSkill.grade})`,
                'value': `std:${item.id}`,
                'ke': item.ke == '1',
                'trigger_effect': item['trigger_effect'],
                'grade': triggerSkill.grade,
            }
        })
        .filter((item) => item.grade <= maxGrade)
        .sort((a, b) => b.grade - a.grade)
}