<template>
  <div>
    <v-tabs>
      <v-tab>词条</v-tab>
      <v-tab>条件</v-tab>
      <v-tab-item>
        <v-container>
          <v-data-table
              dense
              hide-default-footer
              show-group-by
              :calculate-widths="true"
              :headers="attrPrefixHeaders"
              :items="attrPrefixItems"
              :items-per-page="100"
              class="elevation-1 table"
              style="width: 100%"
          ></v-data-table>
        </v-container>
      </v-tab-item>
      <v-tab-item>
        <v-container>
          <v-data-table
              dense
              hide-default-footer
              :calculate-widths="true"
              :headers="conditionHeaders"
              :items="conditionItems"
              :items-per-page="100"
              class="elevation-1 table"
              style="width: 100%"
          ></v-data-table>
        </v-container>
      </v-tab-item>
    </v-tabs>

  </div>

</template>

<script>
import {AttrPrefixes, CONDITIONS} from "@/model/grade/attr";

const attrPrefixHeaders = [
  { text: '名称', value: 'name', groupable: false },
  { text: '组别', value: 'group', groupable: true },
  { text: '通用/特殊', value: 'cmn', groupable: true },
  { text: '标准分', value: 'value', groupable: false },
  { text: '算分规则', value: 'calcType', groupable: true },
  { text: '相关技能', value: 'owners', groupable: false },
];

const conditionHeaders = [
  { text: '名称', value: 'name' },
  { text: '系数', value: 'factor' },
  { text: '说明', value: 'desc' },
  { text: '相关技能', value: 'owners' },
];

export default {
  name: "CalcRuleView",
  data: () => ({
    showDialog: false,
  }),
  computed: {
    attrPrefixHeaders() {
      return attrPrefixHeaders;
    },
    attrPrefixItems() {
      return AttrPrefixes.map((attrPrefix) => {
        return {
          name: attrPrefix.name,
          group: attrPrefix.group,
          value: attrPrefix.value,
          cmn: attrPrefix.hasCalcFlag('cmn') ? '通用' : '特殊',
          calcType: attrPrefix.calcTypeDisplay(),
          owners: this.attrPrefixOwners[attrPrefix.key]?.join('，'),
        }
      });
    },
    conditionHeaders() {
      return conditionHeaders
    },
    conditionItems() {
      return Object.entries(CONDITIONS).map(([key, value]) => {
        return {
          name: key,
          factor: value.factor,
          desc: value.desc,
          owners: this.conditionOwners[key]?.join('，'),
        }
      });
    }
  },
  created() {
    const skills = this.$store.state.SkillSource;
    const conditionOwners = {}
    const attrPrefixOwners = {}
    Object.values(skills).forEach((skill) => {
      if (skill['ext_attr']) {
        skill['ext_attr'].forEach((attr) => {
          if (!attr['values']) return;
          attr['values'].forEach((value) => {
            if (value['attr']) {
              if (attrPrefixOwners[value['attr']]) {
                if (!attrPrefixOwners[value['attr']].includes(skill['name'])) {
                  attrPrefixOwners[value['attr']].push(skill['name']);
                }
              } else {
                attrPrefixOwners[value['attr']] = [skill['name']];
              }
            }
            if (value['condition']) {
              if (conditionOwners[value['condition']]) {
                if (!conditionOwners[value['condition']].includes(skill['name'])) {
                  conditionOwners[value['condition']].push(skill['name']);
                }
              } else {
                conditionOwners[value['condition']] = [skill['name']];
              }
            }
          })
        })
      }
    })
    this.attrPrefixOwners = attrPrefixOwners;
    this.conditionOwners = conditionOwners;
  }
}
</script>

