import {formatMultiLine, header} from "@/utils/DataTableUtil";


export const headers = [
    header('ALL', '', '对话编号', 'id'),
    header('ALL', '', '对话类型', 'type'),
    header('ALL', '', '截取内容', 'text'),
    header('ALL', '', '引用数', 'refCount'),
    header('ALL', '','具体引用', 'refs', {multiline: true}),
]

export function convertValue(item) {
    const newItem = {...item}
    newItem['type'] = item['type'] ? item['type'] == 0 ? '常规对话' : '扩展对话' : ''
    newItem['refCount'] = item['refs'].length
    newItem['refs'] = formatMultiLine(item['refs'], 5)
    return newItem;
}

export const dataFilters =  [
    {
        type: 'select',
        label: '类型',
        value: [ '无引用', '多处引用', '重复对话'],
        selected: '无引用',
        filterFunc: (value, item) => item['group'] === value
    },
]