        :items-source="items"
        :mapper="convertValue"
        :header-labels="headerLabels"
        :extra-filters="dataFilters"
    />
  </v-container>
</template>

<script>
import DataPage from "@/components/DataPage";
import {convertValue, headers, headerLabels, dataFilters} from "@/config/event-config";
export default {
  name: "EventView",
  data() {
    return {
      convertValue,
      headerLabels,
      dataFilters
    };
  },
  computed: {
    headers() {
      return headers;
    },
    items() {
      return Object.values(this.$store.state.EventInfoSource)
    }
  },
  components: {
    DataPage,
  }
}
</script>

