<template>
  <div>
    <TabDataPage
        :name="'hero'"
        :tabs="tabs"
        :headersSource="headers"
        :itemsSource="items"
        :mapper="convertValue"
        :header-labels="headerLabels"
        @tabChange="selectTab"
    />
  </div>
</template>

<script>
import {convertValue, headerLabels, headers, tabs} from "@/config/hero-config";
import TabDataPage from "@/components/TabDataPage";

export default {
  name: "HeroDataView",
  data() {
    return {
      selectType: 'NPC',
      tabs,
      headerLabels,
      convertValue,
    };
  },
  methods: {
    selectTab(value) {
      this.selectType = value;
    },
  },
  computed: {
    headers() {
      return headers
    },
    items() {
      return Object.values(this.$store.state.HeroSource)
    }
  },
  components: {
    TabDataPage,
  }
}
</script>

