<template>
  <v-card>
    <v-card-text>
      <v-form>
        <v-card flat>
          <v-card-title>基本信息</v-card-title>
          <v-card-text>
            <v-row>
              <v-col
                  cols="12"
                  md="2"
              >
                <v-text-field
                    label="人物名" outlined dense
