                ></v-select>
              </v-col>
              <v-col
                  cols="12"
                  md="1"
              >
                <v-select
                    :items="[1,2,4,6,8,10]" outlined dense
                    label="氪度"
                    v-model="role.base.ke"
                    v-on:change="recalc"
                ></v-select>
              </v-col>
              <v-col
                  cols="12"
                  md="5"
              />
              <v-col cols="12" md="1">
                <v-btn v-on:click="submit" :disabled="!modified">
                  <span class="text-subtitle-2">{{isEdit ? '保存' : '创建'}}</span>
                </v-btn>
                <p></p>
                <v-btn v-on:click="reset">
                  <span class="text-subtitle-2">重置</span>
                </v-btn>
              </v-col>

              <v-col
                  cols="12"
                  md="2"
              >
                <v-text-field
                    readonly
                    label="总分"
                    v-model="totalGrade"
                    outlined persistent-hint
                    class="headline centered-input"
                    :hint="role.checkTotalGrade(totalGrade)"
                >
                </v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" md="2">
                <v-checkbox label="双手攻击（1分）" dense hide-details v-model="role.config.twoHands"></v-checkbox>
              </v-col>
              <v-col cols="12" md="2" v-if="role.base.round<4">
                <v-checkbox label="内力调和（1分）" dense hide-details v-model="role.config.harmony"></v-checkbox>
              </v-col>
            </v-row>
            </v-card-text>
        </v-card>
        <v-card flat>
          <v-card-title>人物配置</v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="12" md="6">
                <v-card outlined>
                  <v-card-title class="text-h6">称号</v-card-title>
                  <v-card-text>
                    <v-row dense>
                      <v-col dense cols="12" md="4">
                        <v-autocomplete
                            dense outlined
                            label="称号"
                            persistent-placeholder persistent-hint
                            :items="titleList"
                            v-model="role.config.title"
                            :hint="bans.title"
                            v-on:change="titleSelectChanged"
                        >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="新创" dense disabled hide-details v-model="role.options.titleCustom"
                                    v-on:change="role.config.title=null;titleSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="只看有分" dense hide-details v-model="role.options.titleScoreOnly"
                                    v-on:change="role.config.title=null;titleSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
<!--                        <v-checkbox label="显示描述" dense hide-details v-model="role.options.titleDescShow"></v-checkbox>-->
                      </v-col>
                      <v-col dense cols="12" md="2">
                        <v-text-field
                            label="分数" outlined dense readonly
                            :value="objs.title?.grade?.toFixed(2) || 0"
                            class="centered-input"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                    <v-textarea
                        label="描述" outlined dense readonly
                        rows="3"
                        :value="objs.title?.desc"
                        v-if="role.options.titleDescShow"
                        no-resize
                    >
                    </v-textarea>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col cols="12" md="6">
                <v-card outlined>
                  <v-card-title class="text-h6">天赋</v-card-title>
                  <v-card-text>
                    <v-row dense>
                      <v-col dense cols="12" md="4">
                        <v-autocomplete
                            dense outlined
                            label="天赋"
                            persistent-placeholder persistent-hint
                            :items="talentList"
                            :hint="bans.talent"
                            v-model="role.config.talent"
                            v-on:change="talentSelectChanged"
                        >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="新创" dense hide-details v-model="role.options.talentCustom"
                                    v-on:change="role.config.talent=null;talentSelectChanged()"></v-checkbox>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="只看氪金" dense hide-details v-model="role.options.talentKeOnly"
                                    v-on:change="role.config.talent=null;talentSelectChanged()"
                        />
                      </v-col>
                      <v-col cols="12" md="2">
<!--                        <v-checkbox label="显示描述" dense hide-details v-model="role.options.talentDescShow"></v-checkbox>-->
                      </v-col>
                      <v-col dense cols="12" md="2">
                        <v-text-field
                            label="分数" outlined dense readonly persistent-hint
                            :value="objs.talent?.grade?.toFixed(2) || 0"
                            class="centered-input"
                            :hint="objs.talent?.grade_detail"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                    <v-textarea
                        label="描述" outlined dense readonly no-resize rows="3"
                        :value="objs.talent?.desc"
                        v-if="role.options.talentDescShow"
                    >
                    </v-textarea>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>

            <p/>

            <v-row>
              <v-col cols="12" md="6">
                <v-card outlined>
                  <v-card-title class="text-h6">特技</v-card-title>
                  <v-card-text>
                    <v-row dense>
                      <v-col dense cols="12" md="4">
                        <v-autocomplete
                            dense outlined
                            label="特技"
                            persistent-placeholder persistent-hint
                            :items="effectList"
                            :hint="bans.effect"
                            v-model="role.config.effect"
                            v-on:change="effectSelectChanged"
                        >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="新创" dense hide-details v-model="role.options.effectCustom"
                                    v-on:change="role.config.effect=null;effectSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="只看氪金" dense hide-details v-model="role.options.effectKeOnly"
                                    v-on:change="role.config.effect=null;effectSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
<!--                        <v-checkbox label="显示描述" dense hide-details v-model="role.options.effectDescShow"></v-checkbox>-->
                      </v-col>
                      <v-col dense cols="12" md="2">
                        <v-text-field
                            label="分数" outlined dense readonly persistent-hint
                            :value="objs.effect?.grade?.toFixed(2) || 0"
                            class="centered-input"
                            :hint="objs.effect?.grade_detail"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                    <v-textarea
                        label="描述" outlined dense readonly no-resize rows="3"
                        :value="objs.effect?.desc"
                        v-if="role.options.effectDescShow"
                    >
                    </v-textarea>
                  </v-card-text>
                </v-card>
              </v-col>
              <v-col cols="12" md="6">
                <v-card outlined>
                  <v-card-title class="text-h6">武功触发特技</v-card-title>
                  <v-card-text>
                    <v-row dense>
                      <v-col dense cols="12" md="4">
                        <v-autocomplete v-if="!role.options.triggerCustom"
                                        dense outlined
                                        label="触发武功"
                                        persistent-placeholder persistent-hint
                                        :items="triggerSkillList"
                                        v-model="role.config.triggerSkill"
                                        v-on:change="triggerSkillSelectChanged"
                        >
                        </v-autocomplete>
                        <v-autocomplete v-if="role.options.triggerCustom"
                                        dense outlined
                                        label="指定触发特技"
                                        persistent-placeholder
                                        persistent-hint
                                        :items="triggerEffectList"
                                        v-model="role.config.triggerSkill"
                                        v-on:change="triggerEffectSelectChanged"
                        >
                        </v-autocomplete>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="指定新创" dense hide-details v-model="role.options.triggerCustom"
                                    v-on:change="role.config.triggerSkill=null;role.options.triggerCustom?triggerEffectSelectChanged():triggerSkillSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-checkbox label="只看氪金" dense hide-details v-model="role.options.triggerKeOnly"
                                    v-on:change="role.config.triggerSkill=null;role.options.triggerCustom?triggerEffectSelectChanged():triggerSkillSelectChanged()"/>
                      </v-col>
                      <v-col cols="12" md="2">
<!--                        <v-checkbox label="显示描述" dense hide-details v-model="role.options.triggerDescShow"></v-checkbox>-->
                      </v-col>
                      <v-col dense cols="12" md="2">
                        <v-text-field
                            label="分数" outlined dense readonly persistent-hint
                            :value="triggerSkillGrade"
                            class="centered-input"
                            suffix="*0.5"
                            :hint="objs.trigger?.grade_detail"
                        >
                        </v-text-field>
                      </v-col>
                    </v-row>
                    <v-textarea
                        label="描述" outlined dense readonly no-resize rows="3"
                        :value="objs.trigger?.desc"
                        v-if="role.options.effectDescShow"
                    >
                    </v-textarea>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
            <p/>
          </v-card-text>
        </v-card>
        <p />
        <v-divider />
        <p />
        <v-row dense>
          <v-col cols="12" md="10">
            <v-card flat>
              <v-card-title>提交记录</v-card-title>
              <v-card-text>
                <v-data-table
                    dense
                    show-select
                    hide-default-footer
                    :calculate-widths="true"
                    :headers="tableHeaders"
                    :items="tableItems"
                    :items-per-page="100"
                    class="elevation-1 table"
                    style="width: 100%"
                >
                  <template v-slot:[`item.actions`]="{ item }">
                    <v-icon
                        small
                        class="mr-2"
                        v-on:click="editItem(item)"
                    >
                      mdi-pencil
                    </v-icon>
                    <v-icon
                        small
                        v-on:click="deleteItem(item)"
                    >
                      mdi-delete
                    </v-icon>
                  </template>
                </v-data-table>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-form>
      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title>确认删除[{{deletingItem ? deletingItem.name : ''}}]？</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text v-on:click="dialogDelete=false">取消</v-btn>
            <v-btn color="blue darken-1" text @click="deleteItemConfirm">确认</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>


import {getEffectList, getTalentList, getTitleList, getTriggerSkillList, Role} from "@/model/role-sim";
import {searchSkill} from "@/utils/SourceDataUtil";
import {localSave} from "@/utils/StorageUtil";

const headers = [
  { text: '名称', value: 'name' },
  { text: '称号', value: 'title' },
  { text: '天赋', value: 'talent' },
  { text: '特技', value: 'effect' },
  { text: '触发武功', value: 'trigger' },
  { text: '操作', value: 'actions', sortable: false },
]

export default {
  name: "RoleCustomView",
  data() {
    return {
      role: new Role(),
      modified: false,
      dialogDelete: false,
      deletingItem: null,
      bottom: false,
      objs: {
        title: null,
        talent: null,
        effect: null,
        trigger: null,
      },
      bans: {
        title: '',
        talent: '',
        effect: '',
      }
    }
  },
  methods: {
    save() {
      if (this.modified) {
        localSave('curRole', this.role);
        this.modified = false
      }
    },
    submit() {
      this.$store.dispatch('customRole/updateCustomRole', {name: this.role.base.name, role: new Role(this.role)});
      this.modified = false
    },
    reset() {
      this.role = new Role()
      this.modified = false
      this.objs = {
        title: null,
        talent: null,
        effect: null,
        trigger: null,
      }
      this.modified = false
    },
    recalc() {
      this.titleSelectChanged()
      this.talentSelectChanged()
      this.effectSelectChanged()
      if(this.role.options.triggerCustom) {
        this.triggerEffectSelectChanged()
      } else {
        this.triggerSkillSelectChanged()
      }
    },
    editItem(item) {
      const role = this.$store.state['customRole'].customRoles.find(role => role.name === item.name)
      this.role = new Role(role.role)
      this.titleSelectChanged()
      this.talentSelectChanged()
      this.effectSelectChanged()
      if(this.role.options.triggerCustom) {
        this.triggerEffectSelectChanged()
      } else {
        this.triggerSkillSelectChanged()
      }
      this.modified = false
    },
    deleteItem(item) {
      this.dialogDelete = true
      this.deletingItem = item
    },
    deleteItemConfirm() {
      this.$store.dispatch('customRole/deleteCustomRole', {name: this.deletingItem.name});
      this.dialogDelete = false
    },
    elementChanged() {
      this.modified = true
    },
    titleSelectChanged() {
      this.objs.title = this.role.getTitle()
      this.bans.title = this.role.checkBan('title', this.objs.title?.id || 0)
      this.elementChanged()
    },
    talentSelectChanged() {
      this.objs.talent = this.role.getSkill(this.role.config.talent)
      this.bans.talent = this.role.checkBan('talent', this.objs.talent?.id || 0)
      this.elementChanged()
    },
    effectSelectChanged() {
      this.objs.effect = this.role.getSkill(this.role.config.effect)
      this.bans.effect = this.role.checkBan('effect', this.objs.effect?.id || 0)
      this.elementChanged()
    },
    triggerSkillSelectChanged() {
      const skillSelected = this.role.getSkill(this.role.config.triggerSkill)
      if (skillSelected.id == -1) {
        this.objs.trigger = null
        return
      }
      const skill = searchSkill(skillSelected.id)
      this.objs.trigger = this.role.getSkill(`std:${skill['trigger_effect'][0]}`)
      this.elementChanged()
    },
    triggerEffectSelectChanged() {
      this.objs.trigger = this.role.getSkill(this.role.config.triggerSkill)
      this.elementChanged()
    },
  },
  computed: {
    isEdit() {
      return this.$store.state['customRole'].customRoles.find(role => role.name === this.role.base.name) !== undefined
    },
    tableHeaders() {
      return headers
    },
    tableItems() {
      return this.$store.state['customRole'].customRoles
          .map(({name, role}) => {
            const newRole = new Role(role)
            return {
              id: name,
              name,
              title: newRole.getTitle().name,
              talent: newRole.getSkill(newRole.config.talent).name,
              effect: newRole.getSkill(newRole.config.effect).name,
              trigger: newRole.getSkill(newRole.config.triggerSkill).name,
              action: '删除',
            }
          })
    },
    totalGrade() {
      let grade = 0
      grade += this.objs.title?.grade || 0
      grade += this.objs.talent?.grade || 0
      grade += this.objs.effect?.grade || 0
      grade += this.objs.trigger?.grade * 0.5 || 0
      if (this.role.config.twoHands) {
        grade += 1
      }
      if (this.role.base.round < 4 && this.role.config.harmony) {
        grade += 1
      }
      return grade.toFixed(2)
    },
    titleList() {
      return [{
        'text': '',
        'value': null
      }].concat(getTitleList()
      .filter(title => {
        if (this.role.options.titleScoreOnly) {
          return title.grade > 0
        } else {
          return true
        }
      }))
    },
    talentList() {
      return [{
        'text': '',
        'value': null,
      }].concat(getTalentList(this.role)
          .filter(talent => {
            if (this.role.options.talentKeOnly) {
              return talent.ke
            } else {
              return !talent.ke || this.role.options.talentCustom
            }
          }))
    },
    effectList() {
      return [{
        'text': '',
        'value': null,
      }].concat(getEffectList(this.role)
      .filter(effect => {
        if (this.role.options.effectKeOnly) {
          return effect.ke
        } else {
          return !effect.ke || this.role.options.effectCustom
        }
      }))
    },
    triggerEffectList() {
      return [{
        'text': '',
        'value': null,
      }].concat(getEffectList(this.role))
    },
    triggerSkillList() {
      if (!this.$store.getters['isReady']) {
        return []
      }
      return [{
        'text': '',
        'value': null,
      }].concat(getTriggerSkillList(this.role)
          .filter(skill => {
            if (this.role.options.triggerKeOnly) {
              return skill.ke
            } else {
              return !skill.ke
            }
          })
          .map(skill => {
            return {
              ...skill,
            }
          })
      )},
    triggerSkillHint() {
      if (!this.objs.trigger) {
        return ''
      }
      const skill = searchSkill(this.objs.trigger.id)
      return `触发特技：[${skill?.id}]${skill?.name || ''}`
    },
    triggerSkillGrade() {
      if (!this.objs.trigger) {
        return 0
      }
      return this.objs.trigger.grade.toFixed(2)
    },
  }
}
</script>

<style scoped>
:deep(.centered-input input){
  text-align: center
}
:deep(.modified-input input) {
  color: blue !important;
}
:deep(.v-messages__message) {
  color: red !important;
  text-align: center;
}
</style>