<template>
  <div>
    <TabDataPage
        :name="'skill'"
        :tabs="tabs"
        :headersSource="headers"
        :itemsSource="items"
        :mapper="convertValue"
        :header-labels="headerLabels"
        @tabChange="selectTab"
    />
  </div>
</template>

<script>
import TabDataPage from "@/components/TabDataPage";
import {headers, convertValue, tabs, headerLabels} from "@/config/skill-config";

export default {
  name: "SkillDataView",
  data() {
    return {
      selectType: 'HAND',
      tabs,
      headerLabels,
      convertValue,
    };
  },
  methods: {
    selectTab(value) {
      this.selectType = value;
    },
  },
  computed: {
    headers() {
      return headers
    },
    items() {
      // 将SkillSource和TitleConfigSource合并
      return Object.values(this.$store.state.SkillSource).concat(Object.values(this.$store.state.TitleConfigSource));
    }
  },
  components: {
    TabDataPage,
  }
}
</script>

