<template>
  <DataPage
      :name="'match'"
      :headers-source="headers"
      :items-source="items"
      :mapper="convertValue"
      :header-labels="[]"
      :extra-filters="dataFilters"
  />
</template>

<script>
import DataPage from "@/components/DataPage";
import {convertValue, headers, dataFilters} from "@/config/match-config";
export default {
  name: "MatchView",
  data() {
    return {
      convertValue,
      dataFilters
    };
  },
  computed: {
    headers() {
      return headers;
    },
    items() {
      // 将this.$store.state.ListSource['match1']和this.$store.state.ListSource['match2']合并
      return Object.values(this.$store.state.ListSource['match1']).concat(Object.values(this.$store.state.ListSource['match2']))
    }
  },
  components: {
    DataPage,
  }
}
</script>

