const eventGroups = ['item', 'xw', 'attr', 'team', 'condition', 'battle', 'quest', 'other']

export function convertValue(item) {
    const newItem = {...item}
    // 将设置点/检查点的数组转换为换行符分隔的字符串
    newItem['id'] = '#' + item['id']
    newItem['prev'] = item['prev']
        ?.map((i) => `#${i}:${searchEventName(i)}`)
        .join('\n')
    newItem['next'] = item['next']
        ?.map((i) => `#${i}:${searchEventName(i)}`)
        .join('\n')
    for (const group of eventGroups) {
        if (!item['effects']) {
            continue
        }
        newItem[`eff_${group}`] = item[`effects`][group]
            ?.join('\n')
    }
    return newItem
}

export const dataFilters = [
    {
        type: 'select',
        label: '标签',
        value: [ '世界', '飞雪', '连城', '天龙', '射雕', '白马', '鹿鼎', '笑傲', '书剑', '神雕', '侠客', '倚天', '碧血', '鸳鸯', '越女', '场景', '对话', '离队' ],
        selected: '世界',
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            return item['tags'].includes(value);
        }
    },
]