import {AttrEntries, AttrPrefixMap} from "@/model/grade/attr";
import {convertAttrToGrade} from "@/model/grade/grade-calc";

export class SkillAndConfig {
    constructor(skillConfigs, options) {
        options = options || {};
        this.skillConfigs = {}
        skillConfigs.forEach(skillConfig => {
            this.skillConfigs[skillConfig['skillType']] = skillConfig
        })
        this.rangeFactor = options.rangeFactor || 1
        this.energyGp = options.energyGp || 0
    }
}

export class SkillAttrGroup {
    constructor(options) {
        if (options) {
            this.type = options.type
            this.skillType = options.skillType
            this.extGrade = null
            this.mainAttr = options.mainAttr;
            this.viceAttr = options.viceAttr;
            this.teamAttr = options.teamAttr;
            this.rangeFactor = options.rangeFactor || 1;
            this.energyGp = options.energyGp || 0;
        }
    }
    calcGrade() {
        const ret = {totalGrade: 0, mainGp: 0, viceGp: 0, teamGp: 0, extGp: 0, otherGp: 0}
        if (this.extGrade) {
            if (this.extGrade.fixed) {
                ret['extGp'] = Number(this.extGrade.fixed)
                ret['totalGrade'] = Number(this.extGrade.fixed)
                ret['extDesc'] = this.extGrade.desc
            } else if (this.extGrade.add) {
                ret['extGp'] = Number(this.extGrade.add)
                ret['totalGrade'] = Number(this.extGrade.add)
                ret['extDesc'] = this.extGrade.desc
            }
        }
        if (!this.mainAttr.isEmpty()) {
            ret['mainGrade'] = convertAttrToGrade(this.mainAttr)
            ret['mainGp'] = ret['mainGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['mainGp']
        }
        if (!this.viceAttr.isEmpty()) {
            ret['viceGrade'] = convertAttrToGrade(this.viceAttr)
            ret['viceGp'] = ret['viceGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['viceGp']
        }
        if (!this.teamAttr.isEmpty()) {
            ret['teamGrade'] = convertAttrToGrade(this.teamAttr, this.rangeFactor)
            ret['teamGp'] = ret['teamGrade'].calcTotalGradePoint().final
            ret['totalGrade'] += ret['teamGp']
        }
        if (this.energyGp > 0) {
            ret['otherGp'] += this.energyGp
            ret['totalGrade'] += this.energyGp
        }
        const detailFunc = () => {
            let text = ''
            let mainName = ''
            let parts = []
            const addPart = (name, value) => {
                value = Number(value)
                if (value != 0) {
                    parts.push([name, value.toFixed(2)])
                }
            }
            if (this.type === '光环') {
                mainName = '单体'
                addPart('单体分值', ret.mainGp)
                addPart('光环分值', ret.teamGp)
                text += '光环，'
                //text += `光环，综合分值：${ret.totalGrade.toFixed(2)}， [单体分值：${ret.mainGp.toFixed(2)}，光环分值：${ret.teamGp.toFixed(2)}]\n`
            } else if (this.type === '防功') {
                addPart('特技分值', ret.mainGp)
                addPart('装备分值', ret.viceGp)
                mainName = '特技'
                //text += `防功，综合分值：${ret.totalGrade.toFixed(2)}， [特技分值：${ret.mainGp.toFixed(2)}，装备分值：${ret.viceGp.toFixed(2)}]\n`
                text += '防功，'
            } else {
                mainName = '单体'
                addPart('单体分值', ret.mainGp)
                //text += `综合分值：${ret.totalGrade.toFixed(2)}\n`
            }
            addPart('额外分值', ret.extGp)
            addPart('其他分值', ret.otherGp)
            if (parts.length > 1) {
                text += `综合分值：${ret.totalGrade.toFixed(2)}，[${parts.map(part => `${part[0]}：${part[1]}`).join('，')}]\n`
            } else if (parts.length == 1) {
                text += `${parts.map(part => `${part[0]}：${part[1]}`).join('，')}\n`
            }

            if (ret['mainGrade']) {
                text += '==========================================\n'
                text += ret['mainGrade'].toString(mainName) + '\n'
                text += '------------------------\n'
                text += ret['mainGrade'].detail()
            }
            if (ret['viceGrade']) {
                text += '==========================================\n'
                text += ret['viceGrade'].toString('装备') + '\n'
                text += '------------------------\n'
                text += ret['viceGrade'].detail()
            }
            if (ret['teamGrade']) {
                text += '==========================================\n'
                text += ret['teamGrade'].toString('光环') + '\n'
                text += '------------------------\n'
                text += ret['teamGrade'].detail()
            }
            if (ret['extGp']) {
                text += '==========================================\n'
                text += `额外分值：${ret['extGp'].toFixed(2)}，${ret['extDesc']}\n`
            }
            if (ret['otherGp']) {
                text += '==========================================\n'
                text += `其他分值：特技气格折算${ret['otherGp'].toFixed(2)}\n`
            }
            return text
        }
        ret['detail'] = detailFunc
        return ret
    }
}

export function ConvertSkillToAttrGroup(skillAndConfig, options) {
    options = options || {}
    const mainAttr = new AttrEntries()
    const viceAttr = new AttrEntries()
    const teamAttr = new AttrEntries()

    // 此处规则复杂
    // 第一种情况：该技能只有一条配置
    // 若技能配置类型为团队天赋或团队状态，则同时填充至个人和团队属性中；否则只填充至个人属性中
    // 第二种情况：该技能有多条配置（目前只可能有两条）
    // 又有三种组合
    // 1. 装备技+状态组合，都是防功。处理方法是忽略装备技效果
    // 2. 团队天赋+团队状态组合，都是一些既有个人效果又有团队效果的天赋。
    //    处理方法是：将团队天赋配置填充至个人属性和团队属性中，将团队状态配置填充至团队属性中
    // 3. 状态+状态组合。仅有一个孤例（1004日月凌空）。怀疑是狐狸配错了。处理方法是忽略第一条状态配置
    let type = ''
    // 防功特殊处理
    if (options.defFlag) {
        // 忽略装备技效果
        type = '防功'
        if (options.useFlag) {
            if (skillAndConfig.skillConfigs['状态']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['状态'], mainAttr, 'normal', options)
            }
            if (skillAndConfig.skillConfigs['团队状态']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队状态'], mainAttr, 'normal', options)
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队状态'], teamAttr, 'normal', options)
            }
        } else {
            if (skillAndConfig.skillConfigs['装备技']) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['装备技'], viceAttr, 'normal', options)
            }
        }
    } else if (Object.values(skillAndConfig.skillConfigs).length === 0) {
        // 无配置
    }
    else if (Object.values(skillAndConfig.skillConfigs).length === 1) {
        Object.entries(skillAndConfig.skillConfigs).forEach(([key, value]) => {
            fillSkillConfigToAttrSet(value, mainAttr, 'normal', options)
            if (key == '团队天赋') {
                type = '光环'
                fillSkillConfigToAttrSet(value, teamAttr, 'team_talent', options)
            } else if (key == '团队状态') {
                type = '光环'
                fillSkillConfigToAttrSet(value, teamAttr, 'normal', options)
            }
        })
    } else if (Object.values(skillAndConfig.skillConfigs).length === 2) {
        if (skillAndConfig.skillConfigs['装备技'] && skillAndConfig.skillConfigs['状态']) {
            // 忽略装备技效果
            type = '防功'
            if (options.useFlag) {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['状态'], mainAttr, 'normal', options)
            } else {
                fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['装备技'], viceAttr, 'normal', options)
            }
        } else if (skillAndConfig.skillConfigs['团队天赋'] && skillAndConfig.skillConfigs['团队状态']) {
            type = '光环'
            // 将团队天赋配置填充至个人属性中，将团队状态配置填充至团队属性中
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队天赋'], mainAttr, 'normal', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队天赋'], teamAttr, 'team_talent', options)
            fillSkillConfigToAttrSet(skillAndConfig.skillConfigs['团队状态'], teamAttr, 'team_buff', options)
        } else {
            console.log('技能配置数量异常', skillAndConfig)
            //throw new Error('技能配置数量异常');
        }
    } else {
        throw new Error('技能配置数量异常');
    }
    return new SkillAttrGroup({
        type, skillType: options.skillType,
        mainAttr, viceAttr, teamAttr,
        rangeFactor: skillAndConfig.rangeFactor, energyGp: skillAndConfig.energyGp
    })
}

function fillSkillConfigToAttrSet(sc, as, typ, options) {
    // 普通设置函数，不乘系数，护体直接加倍
    const f = (key, value) => {
        if (value > 0) {
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 加法函数，乘系数，护体直接加倍
    const af = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setValue(key, value)
        }
    }
    // 乘法函数，乘系数，护体与1的差值加倍
    const pf = (key, value) => {
        if (value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setValue(key, value)
        }
    }
    const caf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value *= 2
            }
            as.setClassValue(cls, key, value)
        }
    }
    const cpf = (cls, key, value) => {
        if (cls && value > 0) {
            value = value * 0.01;
            if (options.upFlag) {
                value = value * 2 - 1
            }
            as.setClassValue(cls, key, value)
        }
    }

    if (typ == 'normal' || typ == 'team_talent') {
        pf('裸攻', sc['bodyAtk'])
        pf('裸防', sc['bodyDef'])
        pf('裸轻', sc['bodyAgi'])
        pf('场攻', sc['battleAtk'])
        pf('场防', sc['battleDef'])
        pf('场轻', sc['battleAgi'])
        pf('时序', sc['battleAsp'])
        pf('敌攻', sc['enemyAtk'])
        pf('敌防', sc['enemyDef'])
        pf('敌轻', sc['enemyAgi'])
        pf('敌时', sc['enemyAsp'])
        af('双击', sc['doubleHit'])
        af('暴击', sc['critical'])
        f('移动', sc['moveDistance'])
        f('射程', sc['attackDistance'])
        f('范围', sc['attackRange'])
        af('出招', sc['castInc'])
        caf(sc['specCastIncType'], '出招', sc['specCastInc'])
    }
    if (typ == 'normal' || typ == 'team_buff') {
        pf('命中', sc['hitInc'])
        pf('闪避', sc['hitDec'])
        pf('威力', sc['powerInc'])
        pf('减威', sc['powerDec'])
        pf('伤害', sc['damageInc'])
        pf('减伤', sc['damageDec'])
        cpf(sc['specHitIncType'], '命中', sc['specHitInc'])
        cpf(sc['specHitDecType'], '闪避', sc['specHitDec'])
        cpf(sc['specPowerIncType'], '威力', sc['specPowerInc'])
        cpf(sc['specPowerDecType'], '减威', sc['specPowerDec'])
        cpf(sc['specDamageIncType'], '伤害', sc['specDamageInc'])
        cpf(sc['specDamageDecType'], '减伤', sc['specDamageDec'])
        pf('加受伤', sc['injuryInc'])
        pf('减受伤', sc['injuryDec'])
        af('吸血', sc['bloodSuck'])
        af('反弹', sc['damageCounter'])
        af('挪移', sc['damageTransfer'])
        af('反震', sc['damageReflect'])
        if (sc['deBuffImmune'] && sc['deBuffImmune'].trim() != '') {
            sc['deBuffImmune'].split(' ').map(x => x.trim())
                .forEach(x => as.addImmune(x))
        }
        if (sc['deBuffMake'] && sc['deBuffMake'].trim() != '') {
            let rate = sc['deBuffRate']
            if (options.upFlag) {
                rate *= 2
            }
            const round = sc['deBuffRound']
            sc['deBuffMake'].split(' ').map(x => x.trim())
                .forEach(x => as.addDeBuffMake(x, rate, round))
        }
    }
    // ext
    const ext = sc['ext']

    if (ext) {
        if (ext['values']) {
            ext['values'].forEach(v => {
                const attrConf = AttrPrefixMap[v['attr']]
                const cond = v['condition']
                const rate = v['rate']
                if (attrConf) {
                    let value = Number(v['value'])
                    if (options.upFlag) {
                        if (attrConf.hasCalcFlag('pct')) {
                            value = value * 2 - 1
                        } else {
                            value = value * 2
                        }
                    }
                    if (!attrConf.hasCalcFlag('cmn') || cond || rate) {
                        as.setExtValue(v['attr'], value, rate ? Number(rate) : 1, cond)
                    } else {
                        as.setValue(v['attr'], value)
                    }
                }
            })
        }
        if (ext['classValues']) {
            ext['classValues'].forEach(v => {
                const attrConf = AttrPrefixMap[v['attr']]
                const type = v['type']
                if (attrConf) {
                    let value = Number(v['value'])
                    if (options.upFlag) {
                        if (attrConf.hasCalcFlag('pct')) {
                            value = value * 2 - 1
                        } else {
                            value = value * 2
                        }
                    }
                    as.setClassValue(type, v['attr'], value)
                }
            })
        }
        if (ext['flags']) {
            ext['flags'].forEach(v => {
                const attrConf = AttrPrefixMap[v]
                if (attrConf) {
                    as.setExtValue(v, 1)
                }
            })
        }
        if (ext['debuffs']) {
            ext['debuffs'].forEach(v => {
                let rate = v['rate']
                if (options.upFlag) {
                    rate *= 2
                }
                as.addDeBuffMake(v['attr'], rate*100, v['attr'] == '击退' ? v['value']*100 : v['value'])
            })
        }
        if (ext['immunes']) {
            ext['immunes'].forEach(v => {
                as.addImmune(v)
            })
        }
    }
}

export function calcTeamGradeFactor(auraRange) {
    if (auraRange == 0) {
        return 3
    }

    const dis = auraRange*2 + 1
    return (dis*dis-1) / 800.0
}

export function calcEnergyCostGp(energyCost, isAura) {
    if (isAura) {
        Math.max((7 - energyCost) * 0.2, 0)
    }
    return Math.max((5 - energyCost) * 0.2, 0)
}