import {header} from "@/utils/DataTableUtil";
import {searchEventName, searchHero, searchStage} from "@/utils/SourceDataUtil";


export const headerLabels = [
    { text: '基本', value: 'base', selected: true },
    { text: '阵容', value: 'role', selected: true },
    { text: '统计', value: 'stat', selected: true },
]

export const headers = [
    header('ALL', '', '编号', 'id'),
    header('ALL', '', '名称', 'name'),
    header('ALL', 'base','场景', 'field', {searchable: true }),
    header('ALL', 'base','背景音乐', 'bgm'),
    header('ALL', 'base','相关事件', 'events', {multiline: true}),
    header('ALL', 'stat','敌方人数', 'enemyCnt'),
    header('ALL', 'stat','敌方总等级', 'allEnemyLevel'),
    header('ALL', 'stat','敌方平均等级', 'avgEnemyLevel'),
    header('ALL', 'stat','敌方最高等级', 'maxEnemyLevel'),
    header('ALL', 'stat','敌方最低等级', 'minEnemyLevel'),
    header('ALL', 'role','敌方阵容', 'enemies', {multiline: true}),
    header('ALL', 'role','本方阵容', 'teams', {multiline: true}),
    header('ALL', 'role','友方阵容', 'friends', {multiline: true}),
]

export function convertValue(item, options) {
    const showStyle = options['showStyle'] || []
    const newItem = {...item}
    newItem['field'] = `[${item['field']}]${searchStage(item['field'])['name']}`
    newItem['enemies'] = formatRoles(item['enemies'], showStyle)
    newItem['teams'] = formatRoles(item['teams'], showStyle)
    newItem['friends'] = formatRoles(item['friends'], showStyle)
    newItem['events'] = formatEvents(item['events'])
    newItem['enemyCnt'] = item['enemies'].length
    // 统计敌方总等级
    let totalLevel = 0, maxLevel = 0, minLevel = 999
    item['enemies'].forEach(id => {
        const level = searchHero(id)['level']
        totalLevel += level
        maxLevel = level > maxLevel ? level : maxLevel
        minLevel = level < minLevel ? level : minLevel
    })
    newItem['allEnemyLevel'] = totalLevel
    newItem['maxEnemyLevel'] = maxLevel
    newItem['minEnemyLevel'] = minLevel
    newItem['avgEnemyLevel'] = item['enemies'].length > 0
        ? Math.round(newItem['allEnemyLevel'] * 10 / item['enemies'].length) / 10
        : 0
    return newItem
}

function formatRoles(roleIds, showStyle) {
    // 将角色id列表转换成角色名列表（每行显示5个角色）
    const roles = roleIds.map(id => formatRole(id, showStyle))
        .sort((a, b) => b.level - a.level)
        .reduce((acc, cur, index) => {
            if (index % 5 === 0) {
                acc.push([])
            }
            acc[acc.length - 1].push(cur)
            return acc
        }, [])
        .map(row => row.join('， '))
    return roles.join('\n')
}

function formatRole(roleId, showStyle) {
    const role = searchHero(roleId)
    let ret = ''
    if (showStyle.includes(0)) {
        ret += `[${role.id}]`
    }
    ret += role.name
    if (showStyle.includes(1)) {
        ret += `(${role.level})`
    }
    return ret
}

function formatEvents(eventIds) {
    return eventIds.map(id => {
        return `${searchEventName(id)}`
    }).join('\n')
}