export default {
    constants,
    castConf,
    rangeConf,
    debuffConf,
    immuneConf,
    repelConf,
    classes,
    immuneTags,
    debuffTags,
    getTotalAtk(ae) {
        return ae.getValueInc('裸攻') * constants['标准裸攻'] +
            ae.getValueInc('场攻') * constants['标准场攻']
    },

    getPower(ae) {
        return ae.getValueInc('威力') * constants['标准武功威力']
    },

    getDePower(ae) {
        return -ae.getValueInc('减威') * constants['标准武功威力']
    },

    getClassPower(ae) {
        return classes.map(cls => ae.getClassValueInc(cls, '威力') * constants['标准武功威力'])
    },

    getClassDePower(ae) {
        return classes.map(cls => -ae.getClassValueInc(cls, '减威') * constants['标准武功威力'])
    },

    getTotalDef(ae) {
        return ae.getValueInc('裸防') * constants['标准裸防'] +
            ae.getValueInc('场防') * constants['标准场防']
    },

    getTotalAgi(ae) {
        return ae.getValueInc('裸轻') * constants['标准裸轻'] +
            ae.getValueInc('场轻') * constants['标准场轻']
    },

    getTotalSeriesSpeed(ae) {
        return ae.getValue('裸系') * (constants['标准裸主系'] * 0.2 + constants['标准副系'] * 0.8) +
            ae.getValue('场系') * (constants['标准场主系'] * 0.2 + constants['标准副系'] * 0.8)
    },
}