import {localDelete, localLoad, localSave} from "@/utils/StorageUtil";
import {LearnPortfolio} from "@/model/learn-sim";

const state = () => ({
    learnPortfolios: [],
    curLearnPortfolio: new LearnPortfolio(),
})

const getters = {
    num: state => state.learnPortfolios.length,
}

const actions = {
    async saveLearnPortfolio({ commit }, payload) {
        await commit('saveLearnPortfolio', payload)
    },
    async deleteLearnPortfolio({ commit }, payload) {
        await commit('deleteLearnPortfolio', payload)
        await commit('clearLearnPortfolio')
    },
    async saveLearnPortfolioList({ commit }) {
        await commit('saveLearnPortfolioList')
    },
    async loadLearnPortfolioList({ commit }) {
        await commit('loadLearnPortfolioList')
    },
    async loadLearnPortfolio({ commit }, payload) {
        await commit('loadLearnPortfolio', payload)
    },
    async clearLearnPortfolio({ commit }) {
        await commit('clearLearnPortfolio')
    }
}

const mutations = {
    saveLearnPortfolioList(state) {
        localSave('learnPortfolios', state.learnPortfolios)
    },
    loadLearnPortfolioList(state) {
        state.learnPortfolios = localLoad('learnPortfolios') || []
    },
    loadLearnPortfolio(state, { name }) {
        state.curLearnPortfolio = new LearnPortfolio(localLoad('learnPortfolio::' + name))
    },
    saveLearnPortfolio(state, { name, portfolio }) {
        localSave('learnPortfolio::' + name, portfolio)
        // 如果已经存在，则不再添加，否则把名字添加到列表，并保存列表
        if (state.learnPortfolios.indexOf(name) === -1) {
            state.learnPortfolios.push(name)
            localSave('learnPortfolios', state.learnPortfolios)
        }
    },
    deleteLearnPortfolio(state, { name }) {
        const index = state.learnPortfolios.findIndex(item => item == name)
        if (index === -1) {
            return
        }
        state.learnPortfolios.splice(index, 1)
        localSave('learnPortfolios', state.learnPortfolios)
        localDelete('learnPortfolio::' + name)
    },
    clearLearnPortfolio(state) {
        state.curLearnPortfolio = new LearnPortfolio()
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
}