function formAtkEffMsg(ae) {
    const msgs = []
    addIfExist(msgs, '威力', ae.getValue('威力'))
    addIfExist(msgs, '命中', ae.getValue('命中'))
    addIfExist(msgs, '伤害', ae.getValue('伤害'))
    return solveMsgGroupsByMerge(msgs, FmtRate({symbol: '*', prefix: '武功'}))
}

function formClassAtkEffMsg(ae) {
    const ret = []
    GradeConfig.classes.forEach(c => {
        const msgs = []
        addIfExist(msgs, '威力', ae.getClassValue(c,'威力'))
        addIfExist(msgs, '命中', ae.getClassValue(c, '命中'))
        addIfExist(msgs, '伤害', ae.getClassValue(c, '伤害'))
        ret.push(...solveMsgGroupsByMerge(msgs, FmtRate({symbol: '*', prefix: `${classAliasName[c]}武功`})))
    })
    return ret
}

