function formDebuffAttrMsg(ae) {
    const debuffs = []
    GradeConfig.debuffTags.forEach(t => {
        if (ae.deBuffMakes[t]) {
            ae.deBuffMakes[t].forEach(d => {
                debuffs.push([t, d.rate, d.round])
            })
        }
    })
    if (ae.deBuffMakes['随机']) {
        ae.deBuffMakes['随机'].forEach(d => {
            debuffs.push(['随机不利状态', d.rate, d.round])
        })
    }
    debuffs.sort((a, b) => {
        if (a[2] != b[2]) {
            return b[2] - a[2]
        }
        return b[1] - a[1]
    })

    const ret = []
    let [l, r] = [0, 1]
    while (l < debuffs.length) {
        if (r >= debuffs.length || debuffs[r][2] != debuffs[l][2] || debuffs[r][1] != debuffs[l][1]) {
            const mergedNames = debuffs.slice(l, r).map((debuff) => debuff[0]).join('/')
            ret.push(`攻击造成${mergedNames}${debuffs[l][2]}回合+${(debuffs[l][1]).toFixed(0)}%`)
            l = r
        }
        r++
    }
    return ret
}

function formImmuneAttrMsg(ae) {
    if (Object.values(ae.immunes).length == 0) {
        return []
    }
    const immunes = []
    if (ae.immunes['全部']) {
        immunes.push('持续不利状态')
    }
    GradeConfig.immuneTags.forEach(t => {
        if (ae.immunes[t]) {
            immunes.push(t)
        }
    })
    const immuneNames = immunes.join('/')
    return [`免疫${immuneNames}`]
}

