function addIfExist(msgs, name, value) {
    if (value != 0) {
        msgs.push({name, value})
    }
}

function solveMsgGroups(msgs, format) {
    const ret = []
    msgs.forEach((msg) => {
        ret.push(format(msg.name, msg.value))
    })
    return ret
}

function solveMsgGroupsByMerge(msgs, format, options) {
    options = options || {}
    const asc = options.asc || false
    msgs.sort((a, b) => {
        if (asc) {
            return a.value - b.value
        } else {
            return b.value - a.value
        }
    })

    const ret = []
    let [l, r] = [0, 1]
    while (l < msgs.length) {
       if (r >= msgs.length || msgs[r].value != msgs[l].value) {
           const mergedNames = msgs.slice(l, r).map((msg) => msg.name).join('/')
           ret.push(format(mergedNames, msgs[l].value))
           l = r
       }
       r++
    }
    return ret
}