import {header} from "@/utils/DataTableUtil";
import {searchItem, searchSkill} from "@/utils/SourceDataUtil";
import {skillType, equipPos} from "@/utils/TypeValueConverter";

export const headers = [
    header('ALL', '','配合因子', 'source', {searchable: true }),
    header('ALL', '','因子类型', 'sourceType'),
    header('ALL', '','配合武功', 'matchSkill', {searchable: true }),
    header('ALL', '','基础威力', 'basePower'),
    header('ALL', '','威力加成', 'power'),
]

export function convertValue(item) {
    const newItem = {}
    newItem['power'] = item.power
    const matchSkill = searchSkill(item['matchSkillId'])
    newItem['matchSkill'] = `[${item['matchSkillId']}]${matchSkill.name}`
    newItem['matchSKillData'] = matchSkill
    newItem['basePower'] = matchSkill['attrl_power']
    if (item['itemId']) {
        const data = searchItem(item['itemId'])
        newItem['source'] = `[${item['itemId']}]${data.name}`
        newItem['item'] = data
        newItem['sourceType'] = equipPos(data['position'])
    } else if (item['skillId']) {
        const data = searchSkill(item['skillId'])
        newItem['source'] = `[${item['skillId']}]${data.name}`
        newItem['skill'] = data
        newItem['sourceType'] = skillType(data['type'])
    }
    return newItem
}

export const dataFilters = [
    {
        type: 'select',
        label: '配合种类',
        value: [ '装备', '内功', '轻功', '全部' ],
        selected: '全部',
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            if (value == '装备' && item['item']) {
                return true;
            }
            if (item['skill']) {
                return (skillType(item['skill'].type) == value)
            }
            return false
        }
    },
    {
        type: 'select',
        label: '氪金',
        value: [ '只看氪金', '不看氪金', '全部' ],
        selected: '全部',
        filterFunc: (value, item) => {
            if (value == '全部') {
                return true;
            }
            if (value == '只看氪金') {
                return item['matchSKillData'].ke
            } else {
                return !item['matchSKillData'].ke
            }
        }
    },
]